import { Injectable } from "@angular/core";
import {
  IEmpVacationModel,
  IEmpVacationRequest,
} from "app/personnel-setup/models/emp-balances/vacation";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class EmployeeVacationService extends AppClientService {
  constructor() {
    super();
  }

  update(data: IEmpVacationRequest) {
    return this.post<IResponse<IEmpVacationModel>>(
      "Personnel/Employees/Edit_EmployeeVacationBalance",
      data
    );
  }

  getList(employeeInstanceId: number, params: IPageParams) {
    return this.get<IResponseList<IEmpVacationModel[]>>(
      "Personnel/Employees/GetList_EmployeeVacationBalance",
      {
        params: {
          ...params,
          PersonInstanceID: employeeInstanceId,
        },
      }
    );
  }

  getAll(employeeInstanceId: number) {
    return this.get<IResponse<IEmpVacationModel[]>>(
      "Personnel/Employees/GetAll_EmployeeVacationBalance",
      {
        params: {
          person_instance_id: employeeInstanceId,
        },
      }
    );
  }

  Delete(data: IEmpVacationRequest) {
    return this.delete<IResponse>(
      "Personnel/Employees/Delete_EmployeeVacationBalance",
      {
        body: data,
      }
    );
  }

  getCard(employeeInstanceId: number) {
    return this.get<IResponse<IEmpVacationModel[]>>(
      "Personnel/Employees/GetAllCard_LastVactionBalance",
      {
        params: {
          person_instance_id: employeeInstanceId,
        },
      }
    );
  }

  regenerateBalance(employeeInstanceId: number) {
    return this.post<IResponse>("Personnel/Employees/RegenerateBalance", null, {
      params: {
        person_instance_id: employeeInstanceId,
      },
    });
  }
}
