import { Injectable } from "@angular/core";
import {
  IEditEmployeeBankingAccountRequest,
  IEmployeeBankingAccountDeleteParams,
  IEmployeeBankingAccountModel,
  IEmployeeBankingAccountRequest,
} from "app/personnel-setup/models/payments-information/emp-bank-account";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { IPageParams } from "app/shared/interfaces/params";
import { db } from "app/shared/resources/database-tabels";

@Injectable({
  providedIn: "root",
})
export class EmployeeBankingAccountService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IEmployeeBankingAccountRequest) {
    return this.post<IResponse<IEmployeeBankingAccountModel>>(
      "Personnel/Employees/Create_EmployeeBank",
      data
    );
  }

  update(data: IEditEmployeeBankingAccountRequest) {
    return this.post<IResponse<IEmployeeBankingAccountModel>>(
      "Personnel/Employees/Edit_EmployeeBank",
      data
    );
  }

  getList(employeeInstanceId: number, params: IPageParams) {
    return this.get<IResponseList<IEmployeeBankingAccountModel[]>>(
      "Personnel/Employees/GetList_EmployeeBank",
      {
        params: {
          ...params,
          $filter: `Person_Instance_ID  eq ${employeeInstanceId}`,
          $expand: db.comm_Bank,
        },
      }
    );
  }

  getAll(employeeInstanceId: number) {
    return this.get<IResponse<IEmployeeBankingAccountModel[]>>(
      "Personnel/Employees/GetAll_EmployeeBank",
      {
        params: {
          $filter: `Person_Instance_ID  eq ${employeeInstanceId}`,
          $expand: db.comm_Bank,
        },
      }
    );
  }

  Delete(params: IEmployeeBankingAccountDeleteParams) {
    return this.delete<IResponse>("Personnel/Employees/Delete_EmployeeBank", {
      params: params,
    });
  }
}
