  afterDelete(empWork: IEmpWorkExperienceDisplay) {
    if (empWork.id == this.formCtrls.id.value) this.clearForm();
    if (empWork) this.hasUpdate = true;
  }

  clearForm() {
    this.form.reset({id: 0});
    this.submitted = false;
  }
