import {Component, EventEmitter, OnInit, Output} from "@angular/core";
import {FormGroup, Validators} from "@angular/forms";
import {MatTableDataSource} from "@angular/material/table";
import {IContactTypeModel} from "app/system-setup/hr-setup/models/contact-type";
import {empContactDisplay} from "app/personnel-setup/mapper/emp-contact-address";
import {
  IEmployeeContactDeleteParams,
  IEmployeeContactDisplay,
  IEmployeeContactRequest,
  IEmployeeContactResponse,
} from "app/personnel-setup/models/emp-personal-info/contact-address";
import {EmpGeneralInfoService} from "app/personnel-setup/services/employees/general-info/emp-general-info.service";

import {EmployeeContactService} from "app/personnel-setup/services/employees/personal-info/employee-contact.service";
import {MessageTypes} from "app/shared/enums/message-types.enum";
import {IPageParams} from "app/shared/interfaces/params";
import {IResponse, IResponseList} from "app/shared/interfaces/responces";
import {AppHelpers} from "app/shared/resources/app-helpers";
import {ViewsManager} from "app/shared/resources/views-manager";

@Component({
  selector: "employee-contact-section",
  templateUrl: "./emp-contact-section.component.html",
  styleUrls: ["./emp-contact-section.component.scss"],
})
export class EmployeeContactDialogComponent extends AppHelpers implements OnInit {
  employeeId: number = 0;
  @Output() changUpdated = new EventEmitter<boolean>();

  mandatoryColomns: string[] = [
    "Ser",
    "contactType",
    "contactCategory",
    "contact",
    "primary",
    "display_in_emp_card",
    "actions",
  ];
  optionalColomns: string[] = [];
  dataSource: MatTableDataSource<IEmployeeContactDisplay>;

  isLoaded: boolean = false;

  form: FormGroup;
  submitted: boolean = false;

  constructor(private employeeContactService: EmployeeContactService) {
    super();
    this.rowFunctions = this.getSubRowFunctions(
      this.getParentViewName,
      ViewsManager.emp_contact_address_sec
    );
    this.employeeId = EmpGeneralInfoService.EmployeeId;
  }

  ngOnInit(): void {
    this.form = this.fb.group({
      id: [0, {nonNullable: true}],
      contacT_TYPE_ID: [null, [Validators.required]],
      contacT_Category_ID: [null],
      value: [null, [Validators.required]],
      isPrimary: [null],
      isShow: [null],
    });
  }

  get formCtrls() {
    return this.form.controls;
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany()) {
      if (this.employeeId > 0) this.getList(this.firstPage);
    }
  }

  getList(page: number, pageSize?: number) {
    this.page.pageIndex = page;
    if (pageSize) this.page.dialogSize = pageSize;
    let params: IPageParams = {
      PageNumber: page,
      pageSize: this.page.dialogSize,
    };
    this.employeeContactService
      .GetList(this.employeeId, params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<IEmployeeContactResponse[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(empContactDisplay(res.data));
          this.page.length = res.meta.totalItemCount;
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IEmployeeContactRequest = {
        EmployeeData: {
          Person_ID: this.employeeId,
          Comm_Contact: {
            id: this.form.value.id,
            contacT_TYPE_ID: this.form.value.contacT_TYPE_ID,
            contacT_Category_ID: this.form.value.contacT_Category_ID,
            value: this.form.value.value,
            isVaild: this.form.value.isPrimary ?? false,
            isShow: this.form.value.isShow ?? false,
          },
        },
      };
      model.EmployeeData.Comm_Contact.id == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IEmployeeContactRequest) {
    this.showLoading();
    this.employeeContactService
      .create(model)
      .subscribe((res: IResponse<IEmployeeContactResponse>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getList(this.firstPage);
          this.changUpdated.emit(true);
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  update(model: IEmployeeContactRequest) {
    this.showLoading();
    this.employeeContactService
      .update(model)
      .subscribe((res: IResponse<IEmployeeContactResponse>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.clearForm();
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
          this.getList(this.currentPage);
          this.changUpdated.emit(true);
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  employeeContactToEdit(empContacts: IEmployeeContactDisplay) {
    this.form.patchValue(empContacts);
    this.formCtrls.value.setValue(empContacts.contact);
    this.formCtrls.id.setValue(empContacts.contact_ID);
    this.formCtrls.isPrimary.setValue(empContacts.primary);
    this.formCtrls.isShow.setValue(empContacts.display_in_emp_card);
    // console.log(this.form.value);
  }

  onDelete(employeeContactInfo: IEmployeeContactDisplay): void {
    let msg = employeeContactInfo.contact;
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(employeeContactInfo);
    });
  }

  delete(employeeContactInfo: IEmployeeContactDisplay) {
    let deleteParams: IEmployeeContactDeleteParams = {
      Person_ID: this.employeeId,
      Contact_ID: employeeContactInfo.contact_ID,
    };
    this.showLoading();
    this.employeeContactService.Delete(deleteParams).subscribe((res: IResponse) => {
      if (res.message.messageType == MessageTypes.Success) {
        this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
        if (this.dataSource.data.length == 1 && this.currentPage > 1) {
          // if last item in list: get previous page
          this.getList(this.prevPage, this.page.dialogSize);
        } else {
          // if not last item in list: get current page
          this.getList(this.currentPage, this.page.dialogSize);
        }
        this.changUpdated.emit(true);
        if (employeeContactInfo.contact_ID == this.formCtrls.id.value) this.clearForm();
      } else this.errorList(res.message.messages);
      this.hideLoading();
    });
  }

  clearForm() {
    this.form.reset({id: 0});
    this.submitted = false;
  }

  onChangeContactType(contact: IContactTypeModel[]) {
    if (contact)
      this.formCtrls.value.setValidators([Validators.pattern(contact[0]?.regularExpression)]);
    else this.formCtrls.value.removeValidators([Validators.pattern(contact[0]?.regularExpression)]);
  }
}
