import { Injectable } from "@angular/core";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  IEmpSocialInsuranceDelete,
  IEmpSocialInsuranceModel,
  IEmpSocialInsuranceRequest,
} from "app/personnel-setup/models/emp-employment-info/social-insurance";
import { IPageParams } from "app/shared/interfaces/params";
import { db } from "app/shared/resources/database-tabels";

@Injectable({
  providedIn: "root",
})
export class EmployeeSocialInsuranceService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IEmpSocialInsuranceRequest) {
    return this.post<IResponse<IEmpSocialInsuranceModel>>(
      "Personnel/Employees/Create_EmployeeInsurance",
      data
    );
  }

  update(data: IEmpSocialInsuranceRequest) {
    return this.post<IResponse<IEmpSocialInsuranceModel>>(
      "Personnel/Employees/Edit_EmployeeInsurance",
      data
    );
  }

  getList(employeeId: number, params: IPageParams) {
    return this.get<IResponseList<IEmpSocialInsuranceModel[]>>(
      "Personnel/Employees/GetList_EmployeeInsurance",
      {
        params: {
          ...params,
          $filter: `Person_ID  eq ${employeeId}`,
          $expand: db.core_Insurance_Office,
        },
      }
    );
  }

  getAll(employeeId: number) {
    return this.get<IResponse<IEmpSocialInsuranceModel[]>>(
      "Personnel/Employees/GetAll_EmployeeInsurance",
      {
        params: {
          $filter: `Person_ID eq ${employeeId}`,
          $expand: db.core_Insurance_Office,
        },
      }
    );
  }

  Delete(params: IEmpSocialInsuranceDelete) {
    return this.delete<IResponse>(
      "Personnel/Employees/Delete_EmployeeInsurance",
      {
        params: params,
      }
    );
  }
}
