import { Component, EventEmitter, OnInit, Output } from "@angular/core";
import { IEmployee, IEmployeeRequest } from "app/personnel-setup/models/employees";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import { IGUIModel } from "app/shared/interfaces/user-views";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { ViewsManager } from "app/shared/resources/views-manager";
import { EmployeeGeneralInfoDialogComponent } from "../emp-general-info-dialog/emp-general-info-dialog.component";

@Component({
  selector: "employee-general-info-card",
  templateUrl: "./emp-general-info-card.component.html",
  styleUrls: ["./emp-general-info-card.component.scss"],
})
export class EmployeeGeneralInfoCardComponent extends AppHelpers implements OnInit {
  employeeId: number;
  @Output() changeEmployeeId = new EventEmitter<number>();
  employee: IEmployee;
  isLoaded: boolean = false;
  isCanMadeAction: boolean = false;
  allowedSections: IGUIModel[];

  constructor(private empGeneralInfoService: EmpGeneralInfoService) {
    super();
    this.employeeId = EmpGeneralInfoService.EmployeeId;
  }

  ngOnInit(): void {
    this.employee = this.empGeneralInfoService.employee$.getValue();
    this.empGeneralInfoService.employee$.subscribe((emp) => {
      this.employee = emp;
      this.isLoadedFun();
    });
    this.isLoadedFun();
  }

  isLoadedFun() {
    this.isLoaded = false;
    if (this.employee != null && this.employeeId > 0) this.isLoaded = true;
    if (this.employee == null && this.employeeId == 0) this.isLoaded = true;
  }

  override onModulesChanged() {
    this.allowedSections = this.getCustomViewChildren(
      this.getParentViewName,
      ViewsManager.emp_general_info_tab
    );
  }

  openAddEditDialog(): void {
    if (!this.businessCompanyParams.CompanyID) {
      this.warning("chooseCompanyId");
      return;
    }

    let dialogRef = this.dialogsService.addEditDialog(
      EmployeeGeneralInfoDialogComponent,
      this.employee
    );

    // let dialogRef = this.dialog.open(EmployeeGeneralInfoDialogComponent, {
    // 	data: {
    // 		employee: this.employee,
    // 	},
    // 	position: {
    // 		top: "40px",
    // 	},
    // 	minWidth: "600px",
    // 	panelClass: "dialog-emp-form",
    // 	autoFocus: false,
    // });
    //	after add employee
    // dialogRef.componentInstance.afterAdd.subscribe((employeeInfo: IEmployee) => {
    // 	this.changeEmployeeId.emit(employeeInfo.person.id);
    // 	let url = `/personnel-setup/${this.moduleId}/employees/edit/${employeeInfo.person.id}`;
    // 	this.navigateTo(url);
    // 	this.employee = employeeInfo;
    // });
    // after close dialog
    dialogRef.afterClosed().subscribe((res: { isUpdate: boolean; data: IEmployeeRequest }) => {
      if (res && res.isUpdate) {
        // update case
        //	this.empGeneralInfoService.getEmployeeShared(this.employeeId);
      }
      else if (res && !res.isUpdate) {
        // create case : Go tO update
        let url = `/personnel-setup/${this.moduleId}/employees/edit/${res.data.person.id}`;
        this.router.navigate([url], {queryParams: this.parentParam});
      }
    });
  }
}
