import { Component, Input, OnInit } from "@angular/core";
import { IGUIModel } from "app/shared/interfaces/user-views";
import { ViewsManager } from "app/shared/resources/views-manager";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
	selector: "emp-sap-integration-core",
	templateUrl: "./emp-sap-integration-core.component.html",
	styleUrls: ["./emp-sap-integration-core.component.scss"],
})
export class EmpSapIntegrationCoreComponent extends AppHelpers implements OnInit {
	@Input() icon: string;
	allowedSections: IGUIModel[];
	viewsManager = ViewsManager;

	constructor() {
		super();
	}

	ngOnInit() {}

	override onModulesChanged() {
		this.allowedSections = this.getCustomViewChildren(
			this.getParentViewName,
			ViewsManager.emp_sap_integration_tab
		);
		//  console.log("sap_integration", this.allowedSections);
	}
}
