import { Component, OnInit } from "@angular/core";
import { IResponse } from "app/shared/interfaces/responces";
import { IHoursSummary, IVacationSummary } from "app/personnel-setup/models/emp-employment-info/work-schedule";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { EmployeeWorkScheduleService } from "app/personnel-setup/services/employees/employement-information/employee-work-schedule.service";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";

@Component({
  selector: "app-emp-workschedule-vacations-cards",
  templateUrl: "./emp-workschedule-vacations-cards.component.html",
  styleUrls: ["./emp-workschedule-vacations-cards.component.scss"]
})
export class EmpWorkscheduleVacationsCardsComponent extends AppHelpers implements OnInit {
  employeeInstanceId: number = 0;
  vacationDays: IVacationSummary;
  sickDays: IVacationSummary;
  workingHours: IHoursSummary;
  isLoadedVacation: boolean = false;
  isLoadedSick: boolean = false;
  isLoadedWorking: boolean = false;

  constructor(
    private employeeWorkScheduleService: EmployeeWorkScheduleService,
  ) {
    super();
    this.employeeInstanceId = EmpGeneralInfoService.EmployeeInstanceId;
    this.languageService.isArabic
  }

  ngOnInit(): void {
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany() && this.employeeInstanceId > 0) {
      this.getVacationDays();
      this.getSickDays();
      this.getWorkingDays();
    }
  }


  // Dashboards
  getVacationDays() {
    this.employeeWorkScheduleService
      .getAnnualCasualDashboard(this.employeeInstanceId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IVacationSummary>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.vacationDays = res.data;
          this.isLoadedVacation = true;
        }
        else this.errorList(res.message.messages);
      });
  }

  getSickDays() {
    this.employeeWorkScheduleService
      .getSickLeaveDashboard(this.employeeInstanceId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IVacationSummary>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.sickDays = res.data;
          this.isLoadedSick = true;
        }
        else this.errorList(res.message.messages);
      });
  }

  getWorkingDays() {
    this.employeeWorkScheduleService
      .getWorkingHoursDashboard(this.employeeInstanceId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IHoursSummary>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.workingHours = res.data;
          this.isLoadedWorking = true;
        }
        else this.errorList(res.message.messages);
      });
  }

}
