import { Component, Inject, OnInit } from "@angular/core";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
	selector: "app-employee-contact-and-address-dialog",
	templateUrl: "./emp-contact-and-address-dialog.component.html",
	styleUrls: ["./emp-contact-and-address-dialog.component.scss"],
})
export class EmployeeContactAndAddressDialogComponent extends AppHelpers implements OnInit {
	employeeId: number = 0;
	isContactUpdated: boolean;
	isAddressUpdated: boolean;
	constructor(
		@Inject(MAT_DIALOG_DATA) public data: any,
		public dialogRef: MatDialogRef<EmployeeContactAndAddressDialogComponent>
	) {
		super();
		this.employeeId = EmpGeneralInfoService.EmployeeId;
	}

	ngOnInit(): void {
    this.dialogRef.backdropClick().subscribe((ele) => {
      this.onCloseDialog();
    });
  }

	contactUpdated(value: boolean) {
		this.isContactUpdated = value;
		//  this.data.updateCard();
	}

	addressUpdated(value: boolean) {
		this.isAddressUpdated = value;
		//	this.data.updateCard();
	}

	onCloseDialog() {
		this.dialogRef.close({
			isContactUpdated: this.isContactUpdated,
			isAddressUpdated: this.isAddressUpdated,
		});
	}
}
