import { Injectable } from "@angular/core";
import {
  IContractModel,
  IEmpContractDetailsRequest,
} from "app/personnel-setup/models/emp-employment-info/contract";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { db } from "app/shared/resources/database-tabels";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class EmployeeContractDetailsService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IEmpContractDetailsRequest) {
    return this.post<IResponse<IContractModel>>(
      "Personnel/Employees/Create_EmployeeContract",
      data
    );
  }

  update(data: IEmpContractDetailsRequest) {
    return this.post<IResponse<IContractModel>>(
      "Personnel/Employees/Edit_EmployeeContract",
      data
    );
  }

  getList(employeeId: number, params: IPageParams) {
    return this.get<IResponseList<IContractModel[]>>(
      "Personnel/Employees/GetList_EmployeeContract",
      {
        params: {
          ...params,
          $filter: `Person_ID eq ${employeeId}`,
          $expand: db.core_Contract_Type,
        },
      }
    );
  }

  getAll(employeeId: number) {
    return this.get<IResponse<IContractModel[]>>(
      "Personnel/Employees/GetAll_EmployeeContract",
      {
        params: {
          $filter: `Person_ID eq ${employeeId}`,
          $expand: db.core_Contract_Type,
        },
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>(
      "Personnel/Employees/Delete_EmployeeContract",
      {
        params: { ID: Id },
      }
    );
  }
}
