import { Component, OnInit } from "@angular/core";
import {
  empContactCard,
  employeeAddressToMap,
} from "app/personnel-setup/mapper/emp-contact-address";
import {
  IEmployeeAddress,
  IEmployeeAddressModel,
  IEmployeeContactCard,
  IEmployeeContactResponse,
} from "app/personnel-setup/models/emp-personal-info/contact-address";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import { EmployeeAddressService } from "app/personnel-setup/services/employees/personal-info/employee-address.service";
import { EmployeeContactService } from "app/personnel-setup/services/employees/personal-info/employee-contact.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { EmployeeContactAndAddressDialogComponent } from "../emp-contact-and-address-dialog/emp-contact-and-address-dialog.component";
import { IResponse } from "app/shared/interfaces/responces";

@Component({
  selector: "emp-contact-and-address-card",
  templateUrl: "./emp-contact-and-address-card.component.html",
  styleUrls: ["./emp-contact-and-address-card.component.scss"],
})
export class EmployeeContactAndAddressCardComponent
  extends AppHelpers
  implements OnInit {
  employeeId: number;
  empContacts: IEmployeeContactCard;
  employeeAddressInfo: IEmployeeAddress;

  isContactLoaded: boolean = false;
  isAddressLoaded: boolean = false;

  constructor(
    private employeeContactService: EmployeeContactService,
    private employeeAddressService: EmployeeAddressService
  ) {
    super();
    this.employeeId = EmpGeneralInfoService.EmployeeId;
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    this.empContacts = null;
    this.employeeAddressInfo = null;
    if (this.employeeId > 0 && this.checkNoBusinessOrCompany()) {
      this.GetAllEmpContacts();
      this.GetAllEmpAddress();
    }
    else {
      this.isContactLoaded = true;
      this.isAddressLoaded = true;
    }
  }

  GetAllEmpContacts() {
    return this.employeeContactService
      .GetAll(this.employeeId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IEmployeeContactResponse[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.isContactLoaded = true;
          this.empContacts = empContactCard(res.data);
        }
        else this.errorList(res.message.messages);
      });
  }

  GetAllEmpAddress() {
    return this.employeeAddressService
      .getAll(this.employeeId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IEmployeeAddressModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.isAddressLoaded = true;
          let employeeAddressesFromNewest: IEmployeeAddress[] =
            employeeAddressToMap(res.data).reverse();
          this.employeeAddressInfo =
            employeeAddressesFromNewest.find(
              (address) => address.primary == true
            ) ?? employeeAddressesFromNewest[0];
        }
        else this.errorList(res.message.messages);
      });
  }

  openAddEditDialog(): void {
    this.dialogsService
      .addEditDialog(EmployeeContactAndAddressDialogComponent)
      .afterClosed()
      .subscribe(
        (data: { isAddressUpdated: boolean; isContactUpdated: boolean }) => {
          if (data.isContactUpdated) {
            this.isContactLoaded = false;
            this.GetAllEmpContacts();
          }
          if (data.isAddressUpdated) {
            this.isAddressLoaded = false;
            this.GetAllEmpAddress();
          }
        }
      );
  }

  iteration(a, b): number {
    return a.key - b.key;
  }
}
