import { Injectable } from "@angular/core";
import {
  IEmpLocationDeleteRequest,
  IEmpLocationEditRequest,
  IEmpLocationModel,
  IEmpLocationRequest,
} from "app/personnel-setup/models/emp-employment-info/emp-location";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { IPageParams } from "app/shared/interfaces/params";
import { db } from "app/shared/resources/database-tabels";

@Injectable({
  providedIn: "root",
})
export class EmployeeLocationService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IEmpLocationRequest) {
    return this.post<IResponse<IEmpLocationModel>>(
      "Personnel/Employees/Create_EmployeeLocation",
      data
    );
  }

  update(data: IEmpLocationEditRequest) {
    return this.post<IResponse<IEmpLocationModel>>(
      "Personnel/Employees/Edit_EmployeeLocation",
      data
    );
  }

  getList(personInstancId: number, params: IPageParams) {
    return this.get<IResponseList<IEmpLocationModel[]>>(
      "Personnel/Employees/GetList_EmployeeLocation",
      {
        params: {
          ...params,
          $filter: `Person_Instance_ID eq ${personInstancId}`,
          $expand: db.Core_Location,
        },
      }
    );
  }

  getAll(personInstancId: number) {
    return this.get<IResponse<IEmpLocationModel[]>>(
      "Personnel/Employees/GetAll_EmployeeLocation",
      {
        params: {
          $filter: `Person_Instance_ID eq ${personInstancId}`,
          $expand: db.Core_Location,
        },
      }
    );
  }

  close(params: IEmpLocationDeleteRequest) {
    return this.delete<IResponse>(
      "Personnel/Employees/Close_EmployeeLocation",
      {
        params: params,
      }
    );
  }

  Delete(params: IEmpLocationDeleteRequest) {
    return this.delete<IResponse>(
      "Personnel/Employees/Delete_EmployeeLocation",
      {
        params: params,
      }
    );
  }
}
