import { Injectable } from "@angular/core";
import {
  IEmpMedicalInsuranceModel,
  IEmpMedicalInsuranceRequest,
} from "app/personnel-setup/models/employee-employment-info";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class EmployeeMedicalInsuranceService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IEmpMedicalInsuranceRequest) {
    return this.post<IResponse<IEmpMedicalInsuranceModel>>(
      "Personnel/Employees/Create_dependant",
      data
    );
  }

  update(data: IEmpMedicalInsuranceRequest) {
    return this.post<IResponse<IEmpMedicalInsuranceModel>>(
      "Personnel/Employees/Edit_dependant",
      data
    );
  }

  getAllByEmployeeId(employeeId: number) {
    return this.get<IResponse<IEmpMedicalInsuranceModel[]>>(
      "Personnel/Employees/GetAll_dependant",
      {
        params: {
          $filter: `id eq ${employeeId}`,
        },
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("Personnel/Employees/Delete_dependant", {
      params: { Id: Id },
    });
  }
}
