import { Component, OnInit } from "@angular/core";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { EmpS4HanaDialogComponent } from "../emp-s4-hana-dialog/emp-s4-hana-dialog.component";
import { EmployeeS4HanaService } from "app/personnel-setup/services/employees/sap-integration/employee-s4-hana.service";
import { IEmployeeS4HanaModel } from "app/personnel-setup/models/emp-sap-integration/s4-hana";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";

@Component({
  selector: "app-emp-s4-hana-card",
  templateUrl: "./emp-s4-hana-card.component.html",
  styleUrls: ["./emp-s4-hana-card.component.scss"],
})
export class EmpS4HanaCardComponent extends AppHelpers implements OnInit {
  // employeeId: number;
  employeeInstanceId: number;
  empS4: IEmployeeS4HanaModel;

  isLoaded: boolean = false;
  isTableView: boolean = false;

  constructor(private employeeS4HanaService: EmployeeS4HanaService) {
    super();
    // this.employeeId = EmpGeneralInfoService.EmployeeId;
    this.employeeInstanceId = EmpGeneralInfoService.EmployeeInstanceId;
    EmpGeneralInfoService.employeeInstanceId$.subscribe((empInstanceId) => {
      this.employeeInstanceId = empInstanceId;
      if (this.employeeInstanceId > 0 && this.checkNoBusinessOrCompany()) {
        this.isLoaded = false;
        this.getAllByEmployeeId();
      }
      else this.isLoaded = true;
    });
  }

  ngOnInit(): void {}

  // override onChangeBusinessAndCompanyId(): void {
  //   this.empS4 = null;
  //   if (this.employeeInstanceId > 0 && this.checkNoBusinessOrCompany())
  //     this.getAllByEmployeeId();
  //   else this.isLoaded = true;
  // }

  getAllByEmployeeId() {
    this.empS4 = null;
    this.employeeS4HanaService
      .getAll(this.employeeInstanceId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IEmployeeS4HanaModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          // sort by id (from new to old)
          res.data.sort(
            (a, b) => new Date(b.id).getTime() - new Date(a.id).getTime()
          );
          this.empS4 = res.data[0];
          this.isLoaded = true;
        }
        else this.errorList(res.message.messages);
      });
  }

  openAddEditDialog(): void {
    this.dialogsService
      .addEditDialog(EmpS4HanaDialogComponent)
      .afterClosed()
      .subscribe((updated: boolean) => {
        if (updated) {
          this.isLoaded = false;
          this.getAllByEmployeeId();
        }
      });
  }

  afterDelete(s4Hana: IEmployeeS4HanaModel) {
    if (s4Hana) {
      this.isLoaded = false;
      this.getAllByEmployeeId();
    }
  }

  toggleTable() {
    this.isTableView = !this.isTableView;
  }
}
