import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { empSupervisorDisplay } from "app/personnel-setup/mapper/emp-employment";
import {
  IEmpSupervisorDeleteRequest,
  IEmpSupervisorDisplayModel,
  IEmpSupervisorModel,
  IEmpSupervisorRequest,
} from "app/personnel-setup/models/emp-employment-info/supervisor";
import { EmployeeSupervisorService } from "app/personnel-setup/services/employees/employement-information/employee-supervisor.service";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";
import { ViewsManager } from "app/shared/resources/views-manager";

@Component({
  selector: "emp-supervisor-section",
  templateUrl: "./emp-supervisor-section.component.html",
  styleUrls: ["./emp-supervisor-section.component.scss"],
})
export class EmpSupervisorSectionComponent extends AppHelpers implements OnInit {
  employeeInstanceId: number = 0;
  // @Output() changUpdated = new EventEmitter<boolean>();

  mandatoryColomns: string[] = [
    "supervisor",
    "fromDate",
    "toDate",
    "actions",
  ];
  optionalColomns: string[] = [];
  dataSource: MatTableDataSource<IEmpSupervisorDisplayModel>;

  isLoaded: boolean = false;

  form: FormGroup;
  submitted: boolean = false;

  constructor(private employeeSupervisorService: EmployeeSupervisorService) {
    super();
    this.employeeInstanceId = EmpGeneralInfoService.EmployeeInstanceId;

    this.rowFunctions = this.getSubRowFunctions(
      this.getParentViewName,
      ViewsManager.emp_job_info_supervisor_sec
    );
  }

  ngOnInit(): void {
    this.form = this.fb.group({
      // id: [0, { nonNullable: true }],
      supervisor_id: [null, [Validators.required]],
      from_Date: [null, [Validators.required]],
    });
  }

  get formCtrls() {
    return this.form.controls;
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0) {
      this.dataSource = new MatTableDataSource(null);
    }
    if (this.checkNoBusinessOrCompany() && this.employeeInstanceId > 0) {
      this.getList(this.firstPage);
    }
  }

  getList(page: number, pageSize?: number) {
    this.dataSource = new MatTableDataSource(null);
    this.isLoaded = false;
    this.page.pageIndex = page;
    if (pageSize) {
      this.page.dialogSize = pageSize;
    }
    let params: IPageParams = {
      PageNumber: page,
      pageSize: this.page.dialogSize,
    };
    this.employeeSupervisorService
      .getList(this.employeeInstanceId, params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<IEmpSupervisorModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(
            empSupervisorDisplay(res.data)
          );
          this.page.length = res.meta.totalItemCount;
          this.isLoaded = true;
        }
        else this.errorList(res.message.messages);
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IEmpSupervisorRequest = {
        Person_Instance_ID: this.employeeInstanceId,
        Manager_ID: this.form.value.supervisor_id,
        From_Date: HelpersFunctions.mapDateReverse(this.form.value.from_Date),
      };

      this.create(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IEmpSupervisorRequest) {
    this.showLoading("supervisor");
    this.employeeSupervisorService
      .create(model)
      .subscribe((res: IResponse<IEmpSupervisorModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getList(this.firstPage);
          // this.isUpdated(true);
        }
        else this.errorList(res.message.messages);
        this.hideLoading("supervisor");

      });
  }

  onDelete(supervisor: IEmpSupervisorDisplayModel): void {
    let msg = `${supervisor.supervisor} (${supervisor.fromDate} - ${supervisor.toDate})`;
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) {
        this.delete(supervisor);
      }
    });
  }

  delete(supervisor: IEmpSupervisorDisplayModel) {
    let deleteParams: IEmpSupervisorDeleteRequest = {
      prtid: this.employeeInstanceId,
      mangerid: supervisor.manager_ID,
      fromdate: HelpersFunctions.mapDateReverse(supervisor.from_Date),
      todate: HelpersFunctions.mapDateReverse(supervisor.tO_Date),
    };
    this.showLoading("supervisor");
    this.employeeSupervisorService
      .Delete(deleteParams)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          if (this.dataSource.data.length == 1 && this.currentPage > 1) {
            // if last item in list: get previous page
            this.getList(this.prevPage, this.page.dialogSize);
          }
          else {
            // if not last item in list: get current page
            this.getList(this.currentPage, this.page.dialogSize);
          }
          // this.isUpdated(true);
        }
        else this.errorList(res.message.messages);
        this.hideLoading("supervisor");
      });
  }

  clearForm() {
    this.form.reset(/* { id: 0 } */);
    this.submitted = false;
  }

  // isUpdated(value: boolean) {
  //   this.changUpdated.emit(value);
  // }
}
