import { Component, OnInit } from "@angular/core";
import { empHiringDocumentDisplay } from "app/personnel-setup/mapper/emp-employment";
import { IEmpHiringDocumentDisplayModel, IEmpHiringDocumentModel, } from "app/personnel-setup/models/emp-employment-info/hiring-documents";
import {
  EmployeeHiringDocumentService
} from "app/personnel-setup/services/employees/employement-information/employee-hiring-document.service";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { EmpHiringDocumentsDialogComponent } from "../emp-hiring-documents-dialog/emp-hiring-documents-dialog.component";

@Component({
  selector: "emp-hiring-documents-card",
  templateUrl: "./emp-hiring-documents-card.component.html",
  styleUrls: ["./emp-hiring-documents-card.component.scss"],
})
export class EmpHiringDocumentsCardComponent extends AppHelpers implements OnInit {
  employeeInstanceId: number = 0;
  hiringDocuments: IEmpHiringDocumentDisplayModel[];
  docCompletePercentage: number = 0;

  isLoaded: boolean = false;
  isLoadedPercentage: boolean = false;
  isTableView: boolean = false;

  constructor(
    private employeeHiringDocumentService: EmployeeHiringDocumentService
  ) {
    super();
    this.employeeInstanceId = EmpGeneralInfoService.EmployeeInstanceId;
    EmpGeneralInfoService.employeeInstanceId$.subscribe((empInstanceId) => {
      this.employeeInstanceId = empInstanceId;
      if (this.employeeInstanceId > 0 && this.checkNoBusinessOrCompany()) {
        this.isLoaded = false;
        this.isLoadedPercentage = false;
        this.getAll();
        this.getPercentage();
      }
      else {
        this.isLoaded = true;
        this.isLoadedPercentage = true;
      }
    });
  }

  ngOnInit(): void {}

  // override onChangeBusinessAndCompanyId(): void {
  //   this.hiringDocuments = null;
  //   if (this.employeeInstanceId > 0 && this.checkNoBusinessOrCompany()) {
  //     this.getAll();
  //     this.getPercentage();
  //   }
  //   else {
  //     this.isLoaded = true;
  //     this.isLoadedPercentage = true;
  //   }
  // }

  getAll() {
    this.hiringDocuments = null;
    this.employeeHiringDocumentService
      .getAll(this.employeeInstanceId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IEmpHiringDocumentModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          // get last three documents
          this.hiringDocuments = empHiringDocumentDisplay(res.data);
          //   res.data.length > 3 ? res.data.slice(-3) : res.data
          this.isLoaded = true;
        }
        else this.errorList(res.message.messages);
      });
  }

  getPercentage() {
    this.docCompletePercentage = 0;
    this.employeeHiringDocumentService
      .getCard(this.employeeInstanceId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<number>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.docCompletePercentage = res.data;
          this.isLoadedPercentage = true;
        }
        else this.errorList(res.message.messages);
      });
  }

  openAddEditDialog(): void {
    this.dialogsService
      .addEditDialog(EmpHiringDocumentsDialogComponent)
      .afterClosed()
      .subscribe((updated: boolean) => {
        if (updated) {
          this.isLoaded = false;
          this.isLoadedPercentage = false;
          this.getAll();
          this.getPercentage();
        }
      });
  }

  afterDelete(hiringDocuments: IEmpHiringDocumentDisplayModel) {
    if (hiringDocuments) {
      this.isLoaded = false;
      this.getAll();
    }
  }

  toggleTable() {
    this.isTableView = !this.isTableView;
  }
}
