import { Component, EventEmitter, OnInit, Output } from "@angular/core";
import { MatTableDataSource } from "@angular/material/table";
import {
  IEmpJobDataModel,
  IEmpJobStatusPositionRequest,
  IEmpStatusAppendJobRequest,
  IEmpStatusAssignOrgAndJobRequest
} from "app/personnel-setup/models/employee-employment-info";
import { FormGroup, Validators } from "@angular/forms";
import { ICoreStatusPropertiesModel, IJobStatusModel } from "app/system-setup/hr-setup/models/emp-job-status";
import { IRowActionsModel } from "app/shared/interfaces/user-views";
import {
  EmployeeJobInformationService
} from "app/personnel-setup/services/employees/employement-information/employee-job-information.service";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import { ViewsManager } from "app/shared/resources/views-manager";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";
import { IResponse } from "app/shared/interfaces/responces";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPersonInstanceOrg } from "app/org-management/model/org-structure";
import { IEmployeeJobTitleModel } from "app/org-management/model/job-title";
import {
  ActionType
} from "app/personnel-setup/components/employees/employement-information/emp-job-info/old-emp-job-section/emp-job-section.component";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { Constants } from "app/shared/resources/constants";

@Component({
  selector: "app-emp-job-section",
  templateUrl: "./emp-job-section.component.html",
  styleUrls: ["./emp-job-section.component.scss"]
})
export class EmpJobSectionComponent extends AppHelpers implements OnInit {
  @Output() changUpdated = new EventEmitter<boolean>();
  employeeId: number = 0;
  employeeInstanceId: number = 0;

  displayedColumns: string[] = [
    "status",
    "status_date",
    "status_end_date",
    // "reasons",
    "org",
    "actions",
  ];
  dataSource: MatTableDataSource<IEmpJobDataModel>;

  isLoaded: boolean = false;

  form: FormGroup;
  submitted: boolean = false;
  hasUpdate: boolean = false;
  getList: boolean = false;

  reasonsList: ICoreStatusPropertiesModel[];
  protected readonly Constants = Constants;
  protected readonly actionType = ActionType;


  jobRowFunctions: IRowActionsModel = {basic: [], dropdown: []};
  orgRowFunctions: IRowActionsModel = {basic: [], dropdown: []};
  statusRowFunctions: IRowActionsModel = {basic: [], dropdown: []};
  currentAction: string;

  constructor(
    private employeeJobInformationService: EmployeeJobInformationService,
  ) {
    super();
    this.employeeId = EmpGeneralInfoService.EmployeeId;
    this.employeeInstanceId = EmpGeneralInfoService.EmployeeInstanceId;
    EmpGeneralInfoService.employeeInstanceId$.subscribe(id => this.employeeInstanceId = id);

    this.rowFunctions = this.getSubRowFunctions(
      this.getParentViewName,
      ViewsManager.emp_job_info_sec
    );


    // row functions on (job org nested table)
    this.jobRowFunctions.basic = this.rowFunctions.basic.filter(ele => {
      let fun = ele.name.toLocaleLowerCase();
      return fun.includes(ActionType.edit) || fun.includes(ActionType.delete) || fun.includes(ActionType.close);
    });
    // row functions on (org first nested table)
    this.orgRowFunctions.basic = this.rowFunctions.basic.filter((ele) =>
      !this.jobRowFunctions.basic.map((job) => job.id).includes(ele.id)
    );

    // row functions on (status table)
    this.statusRowFunctions.basic = this.rowFunctions.dropdown;

  }

  ngOnInit(): void {
    this.form = this.fb.group({
      // FORM AS GROUPS
      employee_Status: this.fb.group({
        id: [0, {nonNullable: true}],
        Status_ID: [null, [Validators.required]],
        From_Date: [null, [Validators.required]],
        StatusPropertiesId: [],
        NewDefaultCompany: [],
      }),

      core_Person_Instance_Org: this.fb.group({
        id: [0, {nonNullable: true}],
        orgTypeId: [null],
        person_Instance_Status_ID: [null],
        Org_ID: [null],
        From_Date: [null],
      }),

      core_Person_Instance_Job_Title: this.fb.group({
        id: [0, {nonNullable: true}],
        Job_Title_ID: [null],
        person_Instance_Org_ID: [null],
        From_Date: [null],
        Is_Manpower_Affected: [false],
        policy_groups: []
      }),
    });
  }

  get formCtrls() {
    return this.form.controls;
  }

  get empStatusCtrls() {
    return (this.form.get("employee_Status") as FormGroup).controls;
  }

  get corePersonOrgCtrls() {
    return (this.form.get("core_Person_Instance_Org") as FormGroup).controls;
  }

  get corePersonJobCtrls() {
    return (this.form.get("core_Person_Instance_Job_Title") as FormGroup).controls;
  }

  submit() {
    this.submitted = true;
    this.getList = false;
    if (this.form.valid) {
      const model: IEmpJobStatusPositionRequest = {
        employee_Status: {
          PersonInstanceStatus: {
            id: this.empStatusCtrls.id.value,
            Person_Instance_ID: this.employeeInstanceId,
            Status_ID: this.empStatusCtrls.Status_ID.value,
            From_Date: HelpersFunctions.transformData(this.empStatusCtrls.From_Date.value),
            StatusPropertiesId: this.empStatusCtrls.StatusPropertiesId.value
          },
          NewDefaultCompany: this.empStatusCtrls.NewDefaultCompany.value
        },

        core_Person_Instance_Org: {
          id: this.corePersonOrgCtrls.id.value,
          Person_Instance_ID: this.employeeInstanceId,
          Org_ID: this.corePersonOrgCtrls.Org_ID.value,
          person_Instance_Status_ID: this.corePersonOrgCtrls.person_Instance_Status_ID.value,
          From_Date: HelpersFunctions.transformData(this.corePersonOrgCtrls.From_Date.value)
        },

        core_Person_Instance_Job_Title: {
          id: this.corePersonJobCtrls.id.value,
          Person_Instance_ID: this.employeeInstanceId,
          Job_Title_ID: this.corePersonJobCtrls.Job_Title_ID.value,
          From_Date: HelpersFunctions.transformData(this.corePersonJobCtrls.From_Date.value),
          Is_Manpower_Affected: this.corePersonJobCtrls.Is_Manpower_Affected.value,
          person_Instance_Org_ID: this.corePersonJobCtrls.person_Instance_Org_ID.value,
        },

        SelectedPolicygroup: this.corePersonJobCtrls.policy_groups.value
      };
      //  console.log("model", model);
      if (this.empStatusCtrls.Status_ID.value == Constants.endOfServiceID) {
        model.core_Person_Instance_Job_Title = null;
        model.core_Person_Instance_Org = null;
      }

      // assign
      if (this.currentAction === ActionType.assign) {
        let assignBody: IEmpStatusAssignOrgAndJobRequest = {
          core_Person_Instance_Org: model.core_Person_Instance_Org,
          core_Person_Instance_Job_Title: model.core_Person_Instance_Job_Title,
          SelectedPolicygroup: model.SelectedPolicygroup
        };
        this.assign(assignBody);

      }

      // append
      else if (this.currentAction === ActionType.append) {
        let appendBody: IEmpStatusAppendJobRequest = {
          core_Person_Instance_Job_Title: model.core_Person_Instance_Job_Title,
          SelectedPolicygroup: model.SelectedPolicygroup
        };
        this.append(appendBody);
      }

      // create & edit
      else {
        model.employee_Status.PersonInstanceStatus.id == 0 ? this.create(model) : this.update(model);
      }
    } // else this.toastr.error("General.formNotValid");


  }

  create(model: IEmpJobStatusPositionRequest) {
    this.showLoading("job");
    this.employeeJobInformationService
      .create(model)
      .subscribe((res: IResponse<IEmpJobStatusPositionRequest>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          //  this.getList(this.currentPage);
          this.hasUpdate = true;
          this.getList = true;
          this.changUpdated.emit(true);
        }
        else this.errorList(res.message.messages);
        this.hideLoading("job");
      });
  }

  update(model: IEmpJobStatusPositionRequest) {
    this.showLoading("job");
    this.employeeJobInformationService
      .update(model)
      .subscribe((res: IResponse<IEmpJobStatusPositionRequest>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.clearForm();
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
          //  this.getList(this.currentPage);
          this.hasUpdate = true;
          this.getList = true;
          this.changUpdated.emit(true);
        }
        else this.errorList(res.message.messages);
        this.hideLoading("job");
      });
  }


  append(model: IEmpStatusAppendJobRequest) {
    this.showLoading("job");
    this.employeeJobInformationService
      .appendJob(model)
      .subscribe((res: IResponse<IEmpJobStatusPositionRequest>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.clearForm();
          this.success(res?.message?.messageTypeName);
          // this.getList(this.currentPage);
          this.hasUpdate = true;
          this.getList = true;
          this.changUpdated.emit(true);
        }
        else this.errorList(res.message.messages);
        this.hideLoading("job");
      });
  }

  assign(model: IEmpStatusAssignOrgAndJobRequest) {
    this.showLoading("job");
    this.employeeJobInformationService
      .assignOrgAndJob(model)
      .subscribe((res: IResponse<IEmpJobStatusPositionRequest>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.clearForm();
          this.success(res?.message?.messageTypeName);
          // this.getList(this.currentPage);
          this.hasUpdate = true;
          this.getList = true;
          this.changUpdated.emit(true);
        }
        else this.errorList(res.message.messages);
        this.hideLoading("job");
      });
  }

  jobToEdit(data: { status: IEmpJobDataModel, org: IPersonInstanceOrg, job: IEmployeeJobTitleModel }) {
    let status = data.status;
    let org = data.org;
    let job = data.job;
    // set employee Status Data
    if (status) {
      this.empStatusCtrls.id.setValue(status.id);
      this.empStatusCtrls.Status_ID.setValue(status.status_ID);
      this.empStatusCtrls.From_Date.setValue(status.from_Date);
      this.empStatusCtrls.StatusPropertiesId.setValue(status.statusPropertiesId);
      this.empStatusCtrls.NewDefaultCompany.setValue(status.statusPropertiesId);
      this.corePersonOrgCtrls.person_Instance_Status_ID.setValue(status.id);
    }

    // set core_Person_Instance_Org
    if (org) {
      let org_type_id = org.core_Org?.core_Org_Structure_Org[0]?.orG_TYPE_ID;
      this.corePersonOrgCtrls.id.setValue(org.id);
      this.corePersonOrgCtrls.orgTypeId.setValue(org_type_id);
      // this.corePersonOrgCtrls.Org_ID.setValue(org.org_ID);
      this.corePersonOrgCtrls.From_Date.setValue(org.from_Date);
      this.corePersonJobCtrls.person_Instance_Org_ID.setValue(org.id);

      // this.corePersonOrgCtrls.orgTypeId.valueChanges.subscribe(e => {
      //   console.log("orgTypeId", e);
      //   this.corePersonOrgCtrls.Org_ID.setValue(org.org_ID);
      // });

      setTimeout(() => {
        this.corePersonOrgCtrls.Org_ID.setValue(org.org_ID);
      }, 100);
    }

    // set job
    if (job) {
      this.corePersonJobCtrls.id.setValue(job.id);
      //   this.corePersonJobCtrls.Job_Title_ID.setValue(job.job_Title_ID);
      this.corePersonJobCtrls.From_Date.setValue(job.from_Date);
      this.corePersonJobCtrls.Is_Manpower_Affected.setValue(job.is_Manpower_Affected);

      // this.corePersonOrgCtrls.Org_ID.valueChanges.subscribe(e => {
      //   console.log("Org_ID", e);
      //
      //   if (e > 0) this.corePersonJobCtrls.Job_Title_ID.setValue(job.job_Title_ID);
      // });

      setTimeout(() => {
        this.corePersonJobCtrls.Job_Title_ID.setValue(job.job_Title_ID);
      }, 500);
    }

    // if (this.currentAction == ActionType.assign) {
    //   console.log("###### Status 1");
    //   Object.keys(this.empStatusCtrls).forEach(e => {
    //     this.empStatusCtrls[e].disable();
    //     console.log(this.empStatusCtrls[e]);
    //   });
    // }

    if (this.currentAction == ActionType.append) {
      Object.keys(this.empStatusCtrls).forEach(e => {
        this.empStatusCtrls[e]?.disable();
      });

      //   console.log("###### org");
      //   Object.keys(this.corePersonOrgCtrls).forEach(e => {
      //     this.empStatusCtrls[e]?.disable();
      //     console.log(this.empStatusCtrls[e]);
      //   });
    }

    // this.form.patchValue(employeeJobData);
  }

  // get reasons List when change status
  onChangeStatus(item: IJobStatusModel[]) {
    this.reasonsList = item[0]?.core_Status_Properties;
    if (item[0]?.id == Constants.endOfServiceID) {
      this.empStatusCtrls.StatusPropertiesId.setValidators([Validators.required]);
    }
    else {
      this.empStatusCtrls.StatusPropertiesId.removeValidators([Validators.required]);
      this.empStatusCtrls.StatusPropertiesId.reset();
    }
  }

  afterDelete(status: IEmpJobDataModel) {
    if (status.id == this.empStatusCtrls.id.value) this.clearForm();
    if (status) {
      this.hasUpdate = true;
      this.changUpdated.emit(true);
    }
  }

  getCurrentAction(action: ActionType) {
    this.currentAction = action;
    //
    // if (action == ActionType.assign) this.assignValidators(true);
    // else this.assignValidators(false);
    //
    // if (action == ActionType.append) this.appendValidators(true);
    // else this.appendValidators(false);
  }

  clearForm(fromValidation: boolean = false) {
    this.form.reset({
      employee_Status: {id: 0},
      core_Person_Instance_Org: {id: 0},
      core_Person_Instance_Job_Title: {
        id: 0,
        Is_Manpower_Affected: false
      },
    });
    this.submitted = false;
    if (!fromValidation) this.currentAction = null;
    Object.keys(this.empStatusCtrls).forEach(e => {
      this.empStatusCtrls[e]?.enable();
    });
  }

  assignValidators(doValidate: boolean) {
    if (doValidate) {
      this.empStatusCtrls.id.disable();
      this.empStatusCtrls.Status_ID.disable();
      this.clearForm(true);
      this.corePersonOrgCtrls.Org_ID.setValidators([Validators.required]);
      this.corePersonOrgCtrls.From_Date.setValidators([Validators.required]);
      this.corePersonJobCtrls.Job_Title_ID.setValidators([Validators.required]);
      this.corePersonJobCtrls.From_Date.setValidators([Validators.required]);
      this.corePersonOrgCtrls.Org_ID.updateValueAndValidity();
      this.corePersonOrgCtrls.From_Date.updateValueAndValidity();
      // this.corePersonJobCtrls.Job_Title_ID.updateValueAndValidity();
      // this.corePersonJobCtrls.From_Date.updateValueAndValidity();
    }
    else {
      this.empStatusCtrls.id.enable();
      this.empStatusCtrls.Status_ID.enable();
      this.clearForm(true);

      this.corePersonOrgCtrls.Org_ID.removeValidators([Validators.required]);
      this.corePersonOrgCtrls.From_Date.removeValidators([Validators.required]);
      this.corePersonJobCtrls.Job_Title_ID.removeValidators([Validators.required]);
      this.corePersonJobCtrls.From_Date.removeValidators([Validators.required]);
      this.corePersonOrgCtrls.Org_ID.updateValueAndValidity();
      this.corePersonOrgCtrls.From_Date.updateValueAndValidity();
      this.corePersonJobCtrls.Job_Title_ID.updateValueAndValidity();
      this.corePersonJobCtrls.From_Date.updateValueAndValidity();
    }
  }

  appendValidators(doValidate: boolean) {
    if (doValidate) {
      this.empStatusCtrls.id.disable();
      this.empStatusCtrls.Status_ID.disable();

      this.corePersonJobCtrls.Job_Title_ID.setValidators([Validators.required]);
      this.corePersonJobCtrls.From_Date.setValidators([Validators.required]);
      this.corePersonJobCtrls.Job_Title_ID.updateValueAndValidity();
      this.corePersonJobCtrls.From_Date.updateValueAndValidity();
    }
    else {
      this.empStatusCtrls.id.enable();
      this.empStatusCtrls.Status_ID.enable();


      this.corePersonJobCtrls.Job_Title_ID.removeValidators([Validators.required]);
      this.corePersonJobCtrls.From_Date.removeValidators([Validators.required]);
      this.corePersonJobCtrls.Job_Title_ID.updateValueAndValidity();
      this.corePersonJobCtrls.From_Date.updateValueAndValidity();
    }
  }

}

