import {
  HelpersFunctions,
  msgTranslate,
} from "app/shared/resources/helpers-functions";
import { IEmployeeMaritalModel } from "../models/emp-personal-info/marital-status";
import { IEmployeeMilitaryModel } from "../models/emp-personal-info/military-status";
import { IEmployeeDependantModel } from "../models/emp-personal-info/dependants";
import {
  IEmployeeDocumentDataModel,
  IEmployeeDocumentModel,
} from "../models/emp-personal-info/employee-documents";

export function employeeDependentToMap(
  maritalStatus: IEmployeeDependantModel[]
): IEmployeeDependantModel[] {
  return maritalStatus.map((dependent) => {
    return <IEmployeeDependantModel>{
      ...dependent,
      originalBirthDateFormat: dependent.birthDate,
      dependentName: dependent.flower_Name,
      birthDate: HelpersFunctions.mapDate(dependent.birthDate),
      relationship: msgTranslate(
        dependent.corE_Family_Relation?.ar_Name,
        dependent.corE_Family_Relation?.en_Name
      ),
    };
  });
}

export function employeeMaritalStatusToMap(
  maritalStatus: IEmployeeMaritalModel[]
): IEmployeeMaritalModel[] {
  return maritalStatus.map((status) => {
    return <IEmployeeMaritalModel>{
      ...status,
      originalDateFormat: status.statusDate,
      statusDate: HelpersFunctions.mapDate(status.statusDate),
      maritalStatus: msgTranslate(
        status.comm_Marital_Status?.ar_Name,
        status.comm_Marital_Status?.en_Name
      ),
    };
  });
}

export function employeeMilitaryStatusToMap(
  militaryStatus: IEmployeeMilitaryModel[]
): IEmployeeMilitaryModel[] {
  return militaryStatus.map((military) => {
    return <IEmployeeMilitaryModel>{
      ...military,
      status: msgTranslate(
        military.comm_Military_Status?.ar_Name,
        military.comm_Military_Status?.en_Name
      ),
      grade: msgTranslate(
        military.comm_Military_Grade?.ar_Name,
        military.comm_Military_Grade?.en_Name
      ),
      statusDate: HelpersFunctions.mapDate(military.statusDate),
      endDate: HelpersFunctions.mapDate(military.endDate),
    };
  });
}

export function employeePersonalDocumentToMap(
  data: IEmployeeDocumentModel[]
): IEmployeeDocumentDataModel[] {
  return data.map((doc) => {
    return <IEmployeeDocumentDataModel>{
      ...doc.employeeData,
      documentName:
        doc.employeeData?.core_Personal_Identity_Type?.identity_TypeTital,
      originalIssuingDate: doc.employeeData?.dateof_issue,
      originalExpirationDate: doc.employeeData?.expire_Date,
      dateof_issue: HelpersFunctions.mapDate(doc.employeeData?.dateof_issue),
      expire_Date: HelpersFunctions.mapDate(doc.employeeData?.expire_Date),
      attachment_En_Name: doc.attachment_En_Name,
      attachmentID: doc.attachmentID,
      attachment: doc.attachmentFilePath ? "/" + doc.attachmentFilePath : "",
    };
  });
}
