import { Component, OnInit } from "@angular/core";
import { empContactCard } from "app/personnel-setup/mapper/emp-contact-address";
import {
  IEmployeeContactCard,
  IEmployeeContactDisplay,
  IEmployeeContactResponse,
} from "app/personnel-setup/models/emp-personal-info/contact-address";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import { EmployeeContactService } from "app/personnel-setup/services/employees/personal-info/employee-contact.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { EmpContactDialogComponent } from "../emp-contact-dialog/emp-contact-dialog.component";

@Component({
  selector: "emp-contact-card",
  templateUrl: "./emp-contact-card.component.html",
  styleUrls: ["./emp-contact-card.component.scss"],
})
export class EmpContactCardComponent extends AppHelpers implements OnInit {
  employeeId: number;
  empContacts: IEmployeeContactCard;

  isContactLoaded: boolean = false;
  isTableView: boolean = false;

  constructor(private employeeContactService: EmployeeContactService) {
    super();
    this.employeeId = EmpGeneralInfoService.EmployeeId;
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    this.empContacts = null;
    if (this.employeeId > 0 && this.checkNoBusinessOrCompany()) {
      this.GetAllEmpContacts();
    }
    else {
      this.isContactLoaded = true;
    }
  }

  GetAllEmpContacts() {
    return this.employeeContactService
      .GetAll(this.employeeId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IEmployeeContactResponse[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.isContactLoaded = true;
          this.empContacts = empContactCard(res.data);
        }
        else this.errorList(res.message.messages);
      });
  }

  openAddEditDialog(): void {
    this.dialogsService
      .addEditDialog(EmpContactDialogComponent)
      .afterClosed()
      .subscribe((isUpdated: boolean) => {
        if (isUpdated) {
          this.isContactLoaded = false;
          this.GetAllEmpContacts();
        }
      });
  }

  afterDelete(contact: IEmployeeContactDisplay) {
    if (contact) {
      this.isContactLoaded = false;
      this.GetAllEmpContacts();
    }
  }

  toggleTable() {
    this.isTableView = !this.isTableView;
  }

  iteration(a, b): number {
    return a.key - b.key;
  }
}
