import { Component, EventEmitter, Input, OnChanges, Output, SimpleChanges, } from "@angular/core";
import { MatTableDataSource } from "@angular/material/table";
import { empEducationDisplay } from "app/personnel-setup/mapper/emp-career";
import { IEmployeeEducationDeleteParams, IEmployeeEducationModel, } from "app/personnel-setup/models/employee-career/emp-education";
import { EmpDataTableImplement } from "app/personnel-setup/models/implements/emp-data-table.implement";
import { EmployeeEducationService } from "app/personnel-setup/services/employees/career/employee-education.service";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { ViewsManager } from "app/shared/resources/views-manager";

@Component({
  selector: "emp-education-table",
  templateUrl: "./emp-education-table.component.html",
  styleUrls: ["./emp-education-table.component.scss"],
})
export class EmpEducationTableComponent
  extends AppHelpers
  implements EmpDataTableImplement, OnChanges {
  @Input() hasUpdate: boolean;
  @Input() hideActions: boolean = false;
  @Input() isMatPaginator: boolean = false;
  @Output() rowToEdit = new EventEmitter<IEmployeeEducationModel>();
  @Output() afterDelete = new EventEmitter<IEmployeeEducationModel>();

  mandatoryColomns: string[] = [
    "institution",
    "major",
    "degree",
    "year",
    "gpaGrade",
    "actions",
  ];
  optionalColomns: string[] = [];
  dataSource: MatTableDataSource<IEmployeeEducationModel>;
  isLoaded: boolean = false;
  employeeId: number = 0;

  constructor(private employeeEducationService: EmployeeEducationService) {
    super();
    this.employeeId = EmpGeneralInfoService.EmployeeId;

    this.rowFunctions = this.getSubRowFunctions(
      this.getParentViewName,
      ViewsManager.emp_education_sec
    );
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.hasUpdate && changes.hasUpdate.currentValue && this.employeeId > 0) this.getList(this.firstPage);
    if (this.hideActions) {
      let editIndex = this.rowFunctions.basic.findIndex(
        (e) => e.name.toLowerCase() == "edit"
      );
      this.rowFunctions.basic.splice(editIndex, 1);
    }
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany()) {
      if (this.employeeId > 0) this.getList(this.firstPage);
    }
  }

  getList(page: number, pageSize?: number) {
    this.dataSource = new MatTableDataSource(null);
    this.isLoaded = false;
    this.page.pageIndex = page;
    if (pageSize) this.page.dialogSize = pageSize;
    let params: IPageParams = {
      PageNumber: page,
      pageSize: this.page.dialogSize,
    };
    this.employeeEducationService
      .getList(this.employeeId, params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<IEmployeeEducationModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(
            empEducationDisplay(res.data)
          );
          this.page.length = res.meta.totalItemCount;
          this.isLoaded = true;
        }
        else this.errorList(res.message.messages);
      });
  }

  toEdit(education: IEmployeeEducationModel) {
    this.rowToEdit.emit(education);
  }

  onDelete(education: IEmployeeEducationModel): void {
    let msg = `${education.degree} - ${education.grade}`;
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(education);
    });
  }

  delete(education: IEmployeeEducationModel) {
    let deleteParams: IEmployeeEducationDeleteParams = {
      PersonID: this.employeeId,
      GradeID: education.gradeID,
      AcademicID: education.academicID,
    };
    this.showLoading('table');
    this.employeeEducationService
      .Delete(deleteParams)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          this.afterDelete.emit(education);
          if (this.dataSource.data.length == 1 && this.currentPage > 1) {
            // if last item in list: get previous page
            this.getList(this.prevPage, this.page.dialogSize);
          }
          else {
            // if not last item in list: get current page
            this.getList(this.currentPage, this.page.dialogSize);
          }
        }
        else this.errorList(res.message.messages);
        this.hideLoading('table');
      });
  }
}
