import { Component, OnInit } from "@angular/core";
import { MatTableDataSource } from "@angular/material/table";
import { empActivationDisplay } from "app/personnel-setup/mapper/employees-activation";
import { IEmployeeCardModel } from "app/personnel-setup/models/employees";
import { IEmployeeActivationModel } from "app/personnel-setup/models/employees-activation";
import { EmployeesActivationService } from "app/personnel-setup/services/employees-activation.service";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "app-employees-activation",
  templateUrl: "./employees-activation.component.html",
  styleUrls: ["./employees-activation.component.scss"],
})
export class EmployeesActivationComponent extends AppHelpers implements OnInit {
  mandatoryColomns: string[] = ["Ser", "employee", "job", "actions"];
  optionalColomns: string[] = [];

  dataSource: MatTableDataSource<IEmployeeActivationModel>;
  isLoaded: boolean = false;

  constructor(public employeesActivationService: EmployeesActivationService) {
    super();
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany())
      this.getList(this.pageFromUrl > 0 ? this.pageFromUrl : this.firstPage);
  }

  getList(page: number, pageSize?: number) {
    this.isLoaded = false;
    this.removePageParams();
    this.page.pageIndex = page;
    if (pageSize) this.page.pageSize = pageSize;
    let params: IPageParams = {
      PageNumber: page,
      pageSize: this.page.pageSize,
    };
    this.employeesActivationService
      .getList(params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<IEmployeeActivationModel[]>) => {
        if (res.message.messageType == 1) {
          if (res.data.length == 0 && page > 1) {
            this.getList(this.prevPage);
            return;
          }
          this.dataSource = new MatTableDataSource(
            empActivationDisplay(res.data)
          );
          this.isLoaded = true;
          this.page.length = res.meta.totalItemCount;
        }
        else this.errorList(res.message.messages);
      });
  }

  activate(employee: IEmployeeCardModel) {
    console.log(employee);
    this.showLoading();
    this.employeesActivationService
      .activate(employee.id)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == 1) {
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
          this.getList(this.currentPage);
        }
        else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }
}
