  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    this.getList = false;
    if (this.form.valid) {
      const model: IEmployeeDependantRequest = {
        employeeData: {
          person_ID: this.employeeId,
          id: this.form.value.id,
          flower_Name: this.form.value.name,
          relationship_ID: this.form.value.relationship_ID,
          birthDate: this.form.value.birthDate,
          phoneNumber: this.form.value.phoneNumber,
          isEmergcy: this.form.value.isEmergcy ?? false,
        },
      };

      model.employeeData.id == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IEmployeeDependantRequest) {
    this.showLoading('dialog');
    this.employeeDependantService
      .create(model)
      .subscribe((res: IResponse<IEmployeeDependantModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          // this.getList(this.currentPage);
          this.getList = true;
          this.hasUpdate = true;
        } else this.errorList(res.message.messages);
        this.hideLoading('dialog');
      });
  }

  update(model: IEmployeeDependantRequest) {
    this.showLoading('dialog');
    this.employeeDependantService
      .update(model)
      .subscribe((res: IResponse<IEmployeeDependantModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.clearForm();
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
          //   this.getList(this.currentPage);
          this.getList = true;
          this.hasUpdate = true;
        } else this.errorList(res.message.messages);
        this.hideLoading('dialog');
      });
  }

  employeeDependantToEdit(employeeDependantInfo: IEmployeeDependantModel) {
    this.form.patchValue(employeeDependantInfo);
    this.formCtrls.name.setValue(employeeDependantInfo.flower_Name);
    this.formCtrls.birthDate.setValue(
      employeeDependantInfo.originalBirthDateFormat
    );
    // console.log(this.form.value);
  }

  afterDelete(dependant: IEmployeeDependantModel) {
    if (dependant?.id == this.formCtrls.id.value) this.clearForm();
    if (dependant) this.hasUpdate = true;
  }

  clearForm() {
    this.form.reset({ id: 0 });
    this.submitted = false;
  }

