import { Injectable } from "@angular/core";
import {
  IEditEmployeeMaritalRequest,
  IEmployeeMaritalDeleteParams,
  IEmployeeMaritalModel,
  IEmployeeMaritalRequest,
} from "app/personnel-setup/models/emp-personal-info/marital-status";
import { IResponse } from "app/shared/interfaces/responces";
import { db } from "app/shared/resources/database-tabels";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class EmployeeMaritalDataService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IEmployeeMaritalRequest) {
    return this.post<IResponse<IEmployeeMaritalModel>>(
      "Personnel/Employees/Create_EmployeeMaritalStatus",
      data
    );
  }

  update(data: IEditEmployeeMaritalRequest) {
    return this.post<IResponse<IEmployeeMaritalModel>>(
      "Personnel/Employees/Edit_EmployeeMaritalStatus",
      data
    );
  }

  getAll(employeeId: number) {
    return this.get<IResponse<IEmployeeMaritalModel[]>>(
      "Personnel/Employees/GetAll_EmployeeMaritalStatus",
      {
        params: {
          $expand: db.comm_Marital_Status,
          $filter: `PersonID eq ${employeeId}`,
        },
      }
    );
  }

  Delete(params: IEmployeeMaritalDeleteParams) {
    return this.delete<IResponse>(
      "Personnel/Employees/Delete_EmployeeMaritalStatus",
      {
        params: params,
      }
    );
  }
}
