import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";

@Component({
  selector: "see-more-btn",
  templateUrl: "./see-more-btn.component.html",
  styleUrls: ["./see-more-btn.component.scss"],
})
export class SeeMoreBtnComponent implements OnInit {
  @Input() isTableView: boolean = false;
  @Input() employeeId: number = 0;
  @Output() toggleTable = new EventEmitter();

  constructor() {}

  ngOnInit() {}

  onToggleTable() {
    this.toggleTable.emit();
  }
}
