import { Component, HostListener, Inject, OnInit, ViewChild } from "@angular/core";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { IEmployee } from "app/personnel-setup/models/employees";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import { ICreateDialog } from "app/shared/interfaces/dropdown.interface";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { ViewsManager } from "app/shared/resources/views-manager";
import { IEmpStatusCardDisplayModel } from "app/personnel-setup/models/employee-employment-info";
import { Storage } from "app/shared/resources/local-storage";
import { Languages } from "app/shared/enums/languages.enum";
import { DeviceDetectorService } from "ngx-device-detector";
import { MatTabGroup } from "@angular/material/tabs";

@Component({
  selector: "app-add-employee",
  templateUrl: "./add-employee.component.html",
  styleUrls: ["./add-employee.component.scss"],
})
export class AddEmployeeComponent extends AppHelpers implements OnInit {
  // @ViewChild("tabs", {static: true}) tabs: ElementRef<HTMLDivElement>;
  @ViewChild("tabGroup") tabGroup: MatTabGroup;

  employeeId: number = 0; // get from route
  activeTab: number = 0;
  viewsManager = ViewsManager;
  employee: IEmployee;
  StatusPosition: IEmpStatusCardDisplayModel;
  isEmpDataLoaded: boolean = false;

  // showLeftArrow: boolean = false;
  // showRightArrow: boolean = false;
  customBorderWidth: string = "100%";
  // customLeft: string = "0";

  isOnScrollToSection: boolean = false;
  firstScroll: boolean = true;

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: ICreateDialog,
    public dialogRef: MatDialogRef<AddEmployeeComponent>,
    private empGeneralInfoService: EmpGeneralInfoService,
    public deviceService: DeviceDetectorService,
  ) {
    super();
    this.activatedRoute.params.subscribe((params) => {
      this.employeeId = Number(params.employeeId ?? 0);
      EmpGeneralInfoService.employeeInstanceId$.next(null);
      EmpGeneralInfoService.employeeId$.next(this.employeeId);
      this.onChangeBusinessAndCompanyId();
    });

  }

  ngOnInit(): void {
    // this.employee = this.empGeneralInfoService.employee$.getValue();
    this.empGeneralInfoService.employee$.subscribe((emp) => {
      this.employee = emp;
      // EmpGeneralInfoService.employeeId$.next(emp?.person.id ?? 0);
    });

    // load employee status position
    EmpGeneralInfoService.employeeStatusPosition$.subscribe((info: IEmpStatusCardDisplayModel) => {
      this.StatusPosition = info;
      if (info) this.isEmpDataLoaded = true;
    });
  }

  override onChangeBusinessAndCompanyId() {
    this.empGeneralInfoService.employee$.next(null);
    if (this.checkNoBusinessOrCompany() && this.employeeId > 0) {
      this.empGeneralInfoService.getEmployeeShared(this.employeeId);
    }
  }

  override onModulesChanged() {
    setTimeout(() => {
      // this.checkForOverflow();
      this.updateMatTabsPosition();
    }, 0);
  }

  scrollToSection(event, sectionId: string) {
    // console.log("scroll");
    this.isOnScrollToSection = true;
    event.preventDefault();
    const element = document.getElementById(sectionId);
    //const element = this.el.nativeElement.getElementById(sectionId);
    if (element) {
      element.scrollIntoView({behavior: "smooth", block: "start"});
    }
    setTimeout(() => {
      this.isOnScrollToSection = false;
    }, 1000);
  }

  updateMatTabsPosition() {
    let tabPositions = this.tabGroup._elementRef.nativeElement.getBoundingClientRect();
    if (
      Storage.getLocalLang == Languages.en &&
      (tabPositions.left > 100 ||
        this.tabGroup._elementRef.nativeElement.offsetTop + 120)
    ) {
      // this.customLeft = `${tabPositions.left}px`;
      this.customBorderWidth = `${window.innerWidth - tabPositions.left}px`;
    }
    else if (
      Storage.getLocalLang == Languages.ar &&
      (window.innerWidth - tabPositions.left - tabPositions.width > 100 ||
        this.tabGroup._elementRef.nativeElement.offsetTop + 120)
    ) {
      // this.customLeft = `${
      //   window.innerWidth - tabPositions.left - tabPositions.width
      // }px`;
      this.customBorderWidth = `${
        window.innerWidth -
        (window.innerWidth - tabPositions.left - tabPositions.width)
      }px`;
    }
    else this.customBorderWidth = "100%";
    // console.log(
    //   window.innerWidth, tabPositions, tabPositions.left, window.innerWidth - tabPositions.left - tabPositions.width, this.customBorderWidth
    // );
  }

  onAddNew() {
    this.ngOnDestroy();
  }

  changeEmployeeId(employeeId: number) {
    this.employeeId = employeeId;
  }

  @HostListener("window:scroll", ["$event"])
  onScroll($event) {
    // console.log(this.firstScroll)
    if (this.firstScroll) {
      window.scrollTo(0, 0);
      this.activeTab = 0;
      setTimeout(() => {
        this.firstScroll = false;
      }, 1000);
    }

    let scrollTop = window.pageYOffset;
    const element = document.getElementById("top-tools");

    // // arrow buttons
    // let arrows = document.querySelectorAll(".arrow-button");

    // // add nav-fixed to tabs
    // if (scrollTop >= this.tabs.nativeElement.parentElement.offsetTop + 23) {
    //   this.tabs.nativeElement.classList.add("nav-fixed");
    //   element.classList.add("mt-2");
    //   arrows.forEach(ele => ele.classList.add("arrow-fixed"));
    // }
    // else {
    //   this.tabs.nativeElement.classList.remove("nav-fixed");
    //   element.classList.remove("mt-2");
    //   arrows.forEach(ele => ele.classList.remove("arrow-fixed"));
    // }

    if (scrollTop >= this.tabGroup._elementRef.nativeElement.offsetTop + 120) {
      this.tabGroup._elementRef.nativeElement.classList.add("nav-fixed");
      element.classList.add("mt-2");
    }
    else {
      this.tabGroup._elementRef.nativeElement.classList.remove("nav-fixed");
      element.classList.remove("mt-2");
    }

    this.updateMatTabsPosition();

    if (!this.isOnScrollToSection) {
      // change active tab
      this.customViews?.forEach((sec, index) => {
        const element = document.getElementById(sec.viewName);
        if (scrollTop >= element.offsetTop) {
          this.activeTab = index;
        }
      });
    }
  }

  @HostListener("window:resize", ["$event"])
  onResize() {
    // this.checkForOverflow();
    this.updateMatTabsPosition();
  }


  ngOnDestroy(): void {
    this.empGeneralInfoService.cancelRequest$.next();
    this.empGeneralInfoService.cancelRequest$.complete();
    EmpGeneralInfoService.employeeInstanceId$.next(null);
    //  EmpGeneralInfoService.employeeInstanceId$.complete();
    EmpGeneralInfoService.employeeId$.next(null);
    //  EmpGeneralInfoService.employeeId$.complete();
    this.empGeneralInfoService.employee$.next(null);
    //  this.empGeneralInfoService.employee$.complete();
  }


  // checkForOverflow() {
  //   const tabsContainer = this.tabs.nativeElement;
  //   if (Storage.getLocalLang == Languages.ar) {
  //     this.showRightArrow = tabsContainer.scrollLeft < -5;
  //     this.showLeftArrow =
  //       (window.innerWidth - tabsContainer.offsetLeft) -
  //       tabsContainer.clientWidth +
  //       tabsContainer.scrollLeft >
  //       10;
  //   }
  //   else {
  //     this.showRightArrow =
  //       (window.innerWidth - tabsContainer.offsetLeft) -
  //       tabsContainer.clientWidth -
  //       tabsContainer.scrollLeft >
  //       20;
  //     this.showLeftArrow = tabsContainer.scrollLeft > 20;
  //   }
  //   // console.log(tabsContainer.offsetLeft ,window.innerWidth,tabsContainer.scrollLeft, tabsContainer.scrollWidth, tabsContainer.clientWidth, this.showLeftArrow, this.showRightArrow);
  //   this.customBorderWidth = `${window.innerWidth - tabsContainer.offsetLeft}px`;
  //   this.customLeft = `${tabsContainer.offsetLeft}px`;
  // }
  //
  // scrollLeft() {
  //   this.tabs.nativeElement.scrollLeft -= 200;
  //   setTimeout(() => {
  //     this.checkForOverflow();
  //   }, 200);
  // }
  //
  // scrollRight() {
  //   this.tabs.nativeElement.scrollLeft += 200;
  //   setTimeout(() => {
  //     this.checkForOverflow();
  //   }, 200);
  // }


}
