import {Component, OnInit} from "@angular/core";
import {IEmpMedicalInsuranceModel} from "app/personnel-setup/models/employee-employment-info";
import {IResponse} from "app/shared/interfaces/responces";
import {MessageTypes} from "app/shared/enums/message-types.enum";
import {
  EmployeeMedicalInsuranceService
} from "app/personnel-setup/services/employees/employement-information/employee-medical-insurance.service";
import {AppHelpers} from "app/shared/resources/app-helpers";
import {EmpMedicalAndBloodTypeDialogComponent} from "../emp-blood-type-section/emp-blood-type-section.component";
import {IEmployee} from "app/personnel-setup/models/employees";
import {EmpGeneralInfoService} from "app/personnel-setup/services/employees/general-info/emp-general-info.service";

@Component({
	selector: "app-emp-medical-insurance-card",
	templateUrl: "./emp-medical-insurance-card.component.html",
	styleUrls: ["./emp-medical-insurance-card.component.scss"],
})
export class EmpMedicalInsuranceCardComponent extends AppHelpers implements OnInit {
	employeeId: number = 0;
	employeeInstanceId: number = 0;
	employeeMedicalInsuranceInfo: IEmpMedicalInsuranceModel;
	employee: IEmployee;

	isLoaded: boolean = false;

	constructor(
		private employeeMedicalInsuranceService: EmployeeMedicalInsuranceService,
		private empGeneralInfoService: EmpGeneralInfoService
	) {
		super();
		this.employeeId = EmpGeneralInfoService.EmployeeId;
		this.employeeInstanceId = EmpGeneralInfoService.EmployeeInstanceId;
	}

	ngOnInit(): void {
		this.employee = this.empGeneralInfoService.employee$.getValue();
		this.empGeneralInfoService.employee$.subscribe((emp) => {
			this.employee = emp;
			this.isLoadedFun();
		});
		this.isLoadedFun();
	}

	isLoadedFun() {
		this.isLoaded = false;
		if (this.employee != null && this.employeeId > 0) this.isLoaded = true;
		if (this.employee == null && this.employeeId == 0) this.isLoaded = true;
	}

	override onChangeBusinessAndCompanyId(): void {
		this.employeeMedicalInsuranceInfo = null;
		if (this.employeeId > 0 && this.checkNoBusinessOrCompany()){} //this.getAllByEmployeeId();
		else this.isLoaded = true;
	}

	getAllByEmployeeId() {
		this.employeeMedicalInsuranceInfo = null;
		this.employeeMedicalInsuranceService
			.getAllByEmployeeId(this.employeeId)
      .pipe(this.cancelRequest())
			.subscribe((res: IResponse<IEmpMedicalInsuranceModel[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
					this.employeeMedicalInsuranceInfo = res.data[0];
					this.isLoaded = true;
				} else this.errorList(res.message.messages);
			});
	}

	openAddEditDialog(): void {
		this.dialogsService
			.addEditDialog(EmpMedicalAndBloodTypeDialogComponent)
			.afterClosed()
			.subscribe((updated: boolean) => {
				if (updated) {
					this.isLoaded = false;
					this.getAllByEmployeeId();
				}
			});
	}
}
