import {
  trigger,
  animate,
  style,
  transition,
  state, AnimationTriggerMetadata, group
} from "@angular/animations";


export const slideInOut = [
  trigger("slideInOut", [
    state("1", style({height: "*"})),
    state("0", style({height: "0px"})),
    transition("1 <=> 0", animate(200))
  ])
];

export function FadeIn(timingIn: number, height: boolean = false): AnimationTriggerMetadata {
  return trigger("fadeIn", [
    transition(":enter", [
      style(height ? {opacity: 0, height: 0,} : {opacity: 0,}),
      animate(timingIn, style(height ? {opacity: 1, height: "fit-content"} : {opacity: 1,})),
    ]),
  ]);
}

export function fadeOut(timingOut: number, height: boolean = false): AnimationTriggerMetadata {
  return trigger("fadeOut", [
    transition(":leave", [
      animate(timingOut, style(height ? {opacity: 0, height: 0,} : {opacity: 0,})),
    ])
  ]);
}

export function FadeInOut(timingIn: number, timingOut: number, height: boolean = false): AnimationTriggerMetadata {
  return trigger("fadeInOut", [
    transition(":enter", [
      style(height ? {opacity: 0, height: 0,} : {opacity: 0,}),
      animate(timingIn, style(height ? {opacity: 1, height: "fit-content"} : {opacity: 1,})),
    ]),
    transition(":leave", [
      animate(timingOut, style(height ? {opacity: 0, height: 0,} : {opacity: 0,})),
    ])
  ]);
}

export const SlideInOutAnimation = [
  trigger("slideInOut", [
    state("in", style({
      "max-height": "500px", "opacity": "1", "visibility": "visible"
    })),
    state("out", style({
      "max-height": "0px", "opacity": "0", "visibility": "hidden"
    })),
    transition("in => out", [group([
        animate("400ms ease-in-out", style({
          "opacity": "0"
        })),
        animate("600ms ease-in-out", style({
          "max-height": "0px"
        })),
        animate("700ms ease-in-out", style({
          "visibility": "hidden"
        }))
      ]
    )]),
    transition("out => in", [group([
        animate("1ms ease-in-out", style({
          "visibility": "visible"
        })),
        animate("600ms ease-in-out", style({
          "max-height": "500px"
        })),
        animate("800ms ease-in-out", style({
          "opacity": "1"
        }))
      ]
    )])
  ]),
];
