import { Component, OnInit } from "@angular/core";
import { IEmployeeDocumentDataModel, IEmployeeDocumentModel, } from "app/personnel-setup/models/emp-personal-info/employee-documents";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { EmployeeDocumentsService } from "app/personnel-setup/services/employees/personal-info/employee-documents.service";
import { EmployeeDocumentsDialogComponent } from "../emp-documents-dialog/emp-documents-dialog.component";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import { employeePersonalDocumentToMap } from "app/personnel-setup/mapper/employee-personal-info";

@Component({
  selector: "emp-documents-card",
  templateUrl: "./emp-documents-card.component.html",
  styleUrls: ["./emp-documents-card.component.scss"],
})
export class EmployeeDocumentsCardComponent
  extends AppHelpers
  implements OnInit {
  employeeId: number;
  document: IEmployeeDocumentDataModel;
  isLoaded: boolean = false;
  isTableView: boolean = false;

  constructor(private employeeDocumentsService: EmployeeDocumentsService) {
    super();
    this.employeeId = EmpGeneralInfoService.EmployeeId;
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    this.document = null;
    // if (this.employeeId > 0 && this.checkNoBusinessOrCompany()) this.getAll();
    // else this.isLoaded = true;
  }

  getAll() {
    this.document = null;
    this.employeeDocumentsService
      .getAll(this.employeeId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IEmployeeDocumentModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          let sortedList: IEmployeeDocumentModel[] = res.data ?? [];
          // sort by Issuing date (from new to old)
          sortedList.sort(
            (a, b) =>
              new Date(b.employeeData.dateof_issue).getTime() -
              new Date(a.employeeData.dateof_issue).getTime()
          );
          this.document = employeePersonalDocumentToMap(sortedList)[0];
          this.isLoaded = true;
        }
        else this.errorList(res.message.messages);
      });
  }

  openAddEditDialog(): void {
    this.dialogsService
      .addEditDialog(EmployeeDocumentsDialogComponent)
      .afterClosed()
      .subscribe((updated: boolean) => {
        if (updated) {
           this.isLoaded = false;
          this.getAll();
        }
      });
  }

  afterDelete(document: IEmployeeDocumentDataModel) {
    if (document) {
      this.isLoaded = false;
      this.getAll();
    }
  }

  toggleTable() {
    this.isTableView = !this.isTableView;
  }
}
