import { Component, OnInit } from "@angular/core";
import {
  EmployeeContractDetailsService
} from "app/personnel-setup/services/employees/employement-information/employee-contract-details.service";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { IResponse } from "app/shared/interfaces/responces";
import { IContractModel, IEmpContractDisplayModel } from "app/personnel-setup/models/emp-employment-info/contract";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { empContractDisplay } from "app/personnel-setup/mapper/emp-employment";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import {
  EmpContractDetailsDialogComponent
} from "app/personnel-setup/components/employees/employement-information/emp-contract/emp-contract-section/emp-contract-section.component";

@Component({
  selector: "app-emp-contract-card",
  templateUrl: "./emp-contract-card.component.html",
  styleUrls: ["./emp-contract-card.component.scss"]
})
export class EmpContractCardComponent extends AppHelpers implements OnInit {
  contract: IEmpContractDisplayModel;
  employeeId: number = 0;
  isLoaded: boolean = false;
  isTableView: boolean = false;

  constructor(private employeeContractDetailsService: EmployeeContractDetailsService) {
    super();
    this.employeeId = EmpGeneralInfoService.EmployeeId;
  }

  ngOnInit(): void {
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) {
      if (this.employeeId > 0) this.getAllContractDetails();
      else this.isLoaded = true;
    }
  }

  getAllContractDetails() {
    return this.employeeContractDetailsService
      .getAll(this.employeeId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IContractModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.isLoaded = true;
          // to get last or old contract ??
          this.contract = empContractDisplay(res.data.slice(-1))[0];
          if (this.contract?.autO_RENEWAL) {
            this.contract.contractType +=
              " - " + this.translate("autO_RENEWAL");
          }
        }

        else {
          this.errorList(res.message.messages);
        }
      });
  }


  openAddEditDialog(): void {
    this.dialogsService
      .addEditDialog(EmpContractDetailsDialogComponent)
      .afterClosed()
      .subscribe((updated: boolean) => {
        if (updated) {
          this.isLoaded = false;
          this.getAllContractDetails();
        }
      });
  }


  afterDelete(contract: IEmpContractDisplayModel) {
    if (contract) {
      this.isLoaded = false;
      this.getAllContractDetails();
    }
  }

  toggleTable() {
    this.isTableView = !this.isTableView;
  }

}
