import { empInsuranceOfficeDisplay } from "app/personnel-setup/mapper/emp-employment";
import { Component, OnInit } from "@angular/core";
import { IResponse } from "app/shared/interfaces/responces";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import {
  EmployeeSocialInsuranceService
} from "app/personnel-setup/services/employees/employement-information/employee-social-insurance.service";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { IEmployee } from "app/personnel-setup/models/employees";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import {
  EmpSocialInsuranceDialogComponent
} from "app/personnel-setup/components/employees/employement-information/emp-social-insurance/emp-insurance-office-dialog/emp-social-insurance-dialog.component";
import { IEmpSocialInsuranceModel } from "app/personnel-setup/models/emp-employment-info/social-insurance";

@Component({
  selector: "app-emp-social-insurance-card",
  templateUrl: "./emp-social-insurance-card.component.html",
  styleUrls: ["./emp-social-insurance-card.component.scss"],
})
export class EmpSocialInsuranceCardComponent
  extends AppHelpers
  implements OnInit {
  employeeId: number = 0;
  employeeInstanceId: number = 0;
  emp_social_insurance: IEmpSocialInsuranceModel;
  employee: IEmployee;
  isInsuranceNumLoaded: boolean = false;
  isInsuranceOfficeLoaded: boolean = false;
  isTableView: boolean = false;

  constructor(
    private employeeSocialInsuranceService: EmployeeSocialInsuranceService,
    private empGeneralInfoService: EmpGeneralInfoService
  ) {
    super();
    this.employeeId = EmpGeneralInfoService.EmployeeId;
    this.employeeInstanceId = EmpGeneralInfoService.EmployeeInstanceId;
  }

  ngOnInit(): void {
    this.employee = this.empGeneralInfoService.employee$.getValue();
    this.empGeneralInfoService.employee$.subscribe((emp) => {
      this.employee = emp;
      this.isLoadedFun();
    });
    this.isLoadedFun();
  }

  isLoadedFun() {
    this.isInsuranceNumLoaded = false;
    if (this.employee != null && this.employeeId > 0)
      this.isInsuranceNumLoaded = true;
    if (this.employee == null && this.employeeId == 0)
      this.isInsuranceNumLoaded = true;
  }

  override onChangeBusinessAndCompanyId(): void {
    this.emp_social_insurance = null;
    if (this.employeeId > 0 && this.checkNoBusinessOrCompany()) this.getAll();
    else this.isInsuranceOfficeLoaded = true;
  }

  getAll() {
    this.emp_social_insurance = null;
    this.employeeSocialInsuranceService
      .getAll(this.employeeId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IEmpSocialInsuranceModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          res.data.sort(
            (a, b) =>
              new Date(b.froM_DATE).getTime() - new Date(a.froM_DATE).getTime()
          );
          this.emp_social_insurance = empInsuranceOfficeDisplay(res.data)[0];
          this.isInsuranceOfficeLoaded = true;
        }
        else this.errorList(res.message.messages);
      });
  }

  openAddEditDialog(): void {
    this.dialogsService
      .addEditDialog(EmpSocialInsuranceDialogComponent)
      .afterClosed()
      .subscribe((updated: boolean) => {
        if (updated) {
          this.isInsuranceOfficeLoaded = false;
          this.getAll();
        }
      });
  }

  afterDelete(socialInsurance: IEmpSocialInsuranceModel) {
    if (socialInsurance) {
      this.isInsuranceOfficeLoaded = false;
      this.getAll();
    }
  }

  toggleTable() {
    this.isTableView = !this.isTableView;
  }

}
