import { Injectable } from "@angular/core";
import {
	IEmpWorkExperienceModel,
	IEmpWorkExperienceRequest,
} from "app/personnel-setup/models/employee-career/emp-work-experience";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse } from "app/shared/interfaces/responces";
import { db } from "app/shared/resources/database-tabels";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
	providedIn: "root",
})
export class EmployeeWorkExperienceService extends AppClientService {
	constructor() {
		super();
	}

	create(data: IEmpWorkExperienceRequest) {
		return this.post<IResponse<IEmpWorkExperienceModel>>(
			"Personnel/Employees/Create_EmployeeWorkExperience",
			data
		);
	}

	update(data: IEmpWorkExperienceRequest) {
		return this.post<IResponse<IEmpWorkExperienceModel>>(
			"Personnel/Employees/Edit_EmployeeWorkExperience",
			data
		);
	}

	getList(Person_Instance_ID: number, params: IPageParams) {
		return this.get<IResponse<IEmpWorkExperienceModel[]>>(
			"Personnel/Employees/GetList_EmployeeWorkExperience",
			{
				params: {
					...params,
					$filter: `Person_Instance_ID  eq ${Person_Instance_ID}`,
					$expand: `${db.Core_Person_Instance}/${db.Core_Person}`,
				},
			}
		);
	}

	getAll(Person_Instance_ID: number) {
		return this.get<IResponse<IEmpWorkExperienceModel[]>>(
			"Personnel/Employees/GetAll_EmployeeWorkExperience",
			{
				params: {
					$filter: `Person_Instance_ID  eq ${Person_Instance_ID}`,
					$expand: `${db.Core_Person_Instance}/${db.Core_Person}`,
				},
			}
		);
	}

	Delete(Id: number) {
		return this.delete<IResponse>("Personnel/Employees/Delete_EmployeeWorkExperience", {
			params: { ID: Id },
		});
	}
}
