import { Component, EventEmitter, Input, OnChanges, Output, SimpleChanges } from "@angular/core";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { EmpDataTableImplement } from "app/personnel-setup/models/implements/emp-data-table.implement";
import { MatTableDataSource } from "@angular/material/table";
import { IEmployeeMilitaryDeleteParams, IEmployeeMilitaryModel } from "app/personnel-setup/models/emp-personal-info/military-status";
import { EmployeeMilitaryDataService } from "app/personnel-setup/services/employees/personal-info/employee-military-data.service";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import { ViewsManager } from "app/shared/resources/views-manager";
import { IResponse } from "app/shared/interfaces/responces";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { employeeMilitaryStatusToMap } from "app/personnel-setup/mapper/employee-personal-info";

@Component({
  selector: "app-emp-military-table",
  templateUrl: "./emp-military-table.component.html",
  styleUrls: ["./emp-military-table.component.scss"]
})
export class EmpMilitaryTableComponent extends AppHelpers implements EmpDataTableImplement, OnChanges {
  @Input() hasUpdate: boolean;
  @Input() hideActions: boolean = false;
  @Input() isMatPaginator: boolean = false;
  @Output() rowToEdit = new EventEmitter<IEmployeeMilitaryModel>();
  @Output() afterDelete = new EventEmitter<IEmployeeMilitaryModel>();
  mandatoryColomns: string[] = [
    "status",
    "grade",
    "statusDate",
    "endDate",
    "actions",
  ];
  optionalColomns: string[] = [];
  dataSource: MatTableDataSource<IEmployeeMilitaryModel>;
  employeeId: number = 0;
  isLoaded: boolean = false;

  constructor(private employeeMilitaryDataService: EmployeeMilitaryDataService,) {
    super();
    this.employeeId = EmpGeneralInfoService.EmployeeId;
    this.rowFunctions = this.getSubRowFunctions(
      this.getParentViewName,
      ViewsManager.emp_military_data_sec
    );
  }

  getList(page: number, pageSize?: number): void {
    throw new Error("Method not implemented.");
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.hasUpdate && changes.hasUpdate.currentValue && this.employeeId > 0) this.getAll();
    // if (this.hideActions) {
    //   let editIndex = this.rowFunctions.basic.findIndex(e => e.name.toLowerCase() == "edit");
    //   this.rowFunctions.basic.splice(editIndex, 1);
    // }
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany()) {
      if (this.employeeId > 0) this.getAll();
    }
  }

  getAll() {
    this.dataSource = new MatTableDataSource(null);
    this.isLoaded = false;
    this.employeeMilitaryDataService
      .getAll(this.employeeId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IEmployeeMilitaryModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(
            employeeMilitaryStatusToMap(res.data)
          );
          this.isLoaded = true;
        }
        else this.errorList(res.message.messages);
      });
  }


  onDelete(employeeMilitaryInfo: IEmployeeMilitaryModel): void {
    let msg = employeeMilitaryInfo.status;
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(employeeMilitaryInfo);
    });
  }

  delete(employeeMilitaryInfo: IEmployeeMilitaryModel) {
    let deleteParams: IEmployeeMilitaryDeleteParams = {
      PersonID: this.employeeId,
      MilitaryID: employeeMilitaryInfo.militaryID,
    };
    this.showLoading('table');
    this.employeeMilitaryDataService
      .Delete(deleteParams)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.afterDelete.emit(employeeMilitaryInfo);
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
        }
        else this.errorList(res.message.messages);
        this.hideLoading('table');
      });
  }


  toEdit(Military: IEmployeeMilitaryModel) {
    this.rowToEdit.emit(Military);
  }

}
