import { Component, OnInit, Output, EventEmitter } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { employeeAddressToMap } from "app/personnel-setup/mapper/emp-contact-address";
import {
	IEmployeeAddress,
	IEmployeeAddressModel,
	IEmployeeAddressRequest,
	IEmployeeAddressDeleteParams,
} from "app/personnel-setup/models/emp-personal-info/contact-address";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import { EmployeeAddressService } from "app/personnel-setup/services/employees/personal-info/employee-address.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { ViewsManager } from "app/shared/resources/views-manager";

@Component({
	selector: "employee-address-section",
	templateUrl: "./emp-address-section.component.html",
	styleUrls: ["./emp-address-section.component.scss"],
})
export class EmployeeAddressDialogComponent extends AppHelpers implements OnInit {
	employeeId: number = 0;
	@Output() changUpdated = new EventEmitter<boolean>();

	mandatoryColomns: string[] = [
		"Ser",
		// "id",
		"country",
		"governorate",
		"address",
		"primary",
		"actions",
	];
	optionalColomns: string[] = [];
	dataSource: MatTableDataSource<IEmployeeAddress>;

	isLoaded: boolean = false;

	form: FormGroup;
	submitted: boolean = false;

	constructor(private employeeAddressService: EmployeeAddressService) {
		super();
		this.employeeId = EmpGeneralInfoService.EmployeeId;

		this.rowFunctions = this.getSubRowFunctions(
			this.getParentViewName,
			ViewsManager.emp_contact_address_sec
		);
	}

	ngOnInit(): void {
		this.form = this.fb.group({
			id: [0, { nonNullable: true }],
			countryId: [null, [Validators.required]],
			Governorate_ID: [null, [Validators.required]],
			address: [null, [Validators.required]],
			primary: [null],
		});
	}

	get formCtrls() {
		return this.form.controls;
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
		if (this.employeeId > 0 && this.checkNoBusinessOrCompany()) {
			this.getList(this.firstPage);
		}
	}

	getList(page: number, pageSize?: number) {
		this.page.pageIndex = page;
		if (pageSize) this.page.dialogSize = pageSize;
		let params: IPageParams = {
			PageNumber: page,
			pageSize: this.page.dialogSize,
		};
		this.employeeAddressService
			.GetList(this.employeeId, params)
      .pipe(this.cancelRequest())
			.subscribe((res: IResponseList<IEmployeeAddressModel[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
					this.dataSource = new MatTableDataSource(employeeAddressToMap(res.data));
					this.page.length = res.meta.totalItemCount;
					this.isLoaded = true;
				} else this.errorList(res.message.messages);
			});
	}

	submit() {
		// console.log(this.form.value);
		this.submitted = true;
		if (this.form.valid) {
			const model: IEmployeeAddressRequest = {
				EmployeeData: {
					Person_ID: this.employeeId,
					Comm_Address: {
						ID: this.form.value.id,
						Governorate_ID: this.form.value.Governorate_ID,
						En_Name: this.form.value.address,
						IsVaild: this.form.value.primary ?? false,
					},
				},
			};

			model.EmployeeData.Comm_Address.ID == 0 ? this.create(model) : this.update(model);
		} // else this.toastr.error("General.formNotValid");
	}

	create(model: IEmployeeAddressRequest) {
		this.showLoading();
		this.employeeAddressService.create(model).subscribe((res: IResponse<IEmployeeAddressModel>) => {
			if (res?.message?.messageType == MessageTypes.Success) {
				this.clearForm();
				this.addSuccessfullyMsg(res?.message?.messageTypeName);
				this.getList(this.firstPage);
				this.changUpdated.emit(true);
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	update(model: IEmployeeAddressRequest) {
		this.showLoading();
		this.employeeAddressService.update(model).subscribe((res: IResponse<IEmployeeAddressModel>) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.clearForm();
				this.updateSuccessfullyMsg(res?.message?.messageTypeName);
				this.getList(this.currentPage);
				this.changUpdated.emit(true);
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	employeeAddressToEdit(employeeAddressInfo: IEmployeeAddress) {
		this.form.patchValue(employeeAddressInfo);
		this.formCtrls.countryId.setValue(employeeAddressInfo.comm_Governorate.comm_Country.id);
		this.formCtrls.Governorate_ID.setValue(employeeAddressInfo.governorate_ID);
	}

	onDelete(employeeAddressInfo: IEmployeeAddress): void {
		let msg = employeeAddressInfo.address;
		this.deleteDialog(msg).subscribe((confirm) => {
			if (confirm) this.delete(employeeAddressInfo);
		});
	}

	delete(employeeAddressInfo: IEmployeeAddress) {
		let deleteParams: IEmployeeAddressDeleteParams = {
			Person_ID: this.employeeId,
			ADDRESS_ID: employeeAddressInfo.id,
		};
		this.showLoading();
		this.employeeAddressService.Delete(deleteParams).subscribe((res: IResponse) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
				if (this.dataSource.data.length == 1 && this.currentPage > 1) {
					// if last item in list: get previous page
					this.getList(this.prevPage, this.page.dialogSize);
				} else {
					// if not last item in list: get current page
					this.getList(this.currentPage, this.page.dialogSize);
				}
				this.changUpdated.emit(true);
				if (employeeAddressInfo.id == this.formCtrls.id.value) this.clearForm();
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	clearForm() {
		this.form.reset({ id: 0 });
		this.submitted = false;
	}
}
