import { msgTranslate } from "app/shared/resources/helpers-functions";
import {
  IEmployeeAddress,
  IEmployeeAddressModel,
  IEmployeeContactCard,
  IEmployeeContactDisplay,
  IEmployeeContactModel,
  IEmployeeContactResponse,
} from "../models/emp-personal-info/contact-address";

export function empContactDisplay(
  empContacts: IEmployeeContactResponse[]
): IEmployeeContactDisplay[] {
  return empContacts.map((contact) => {
    return <IEmployeeContactDisplay>{
      contactType: msgTranslate(
        contact?.comm_Contact?.comm_Contact_Type?.ar_Name,
        contact?.comm_Contact?.comm_Contact_Type?.en_Name
      ),
      contactCategory: msgTranslate(
        contact?.comm_Contact?.comm_Contact_Category?.ar_Name,
        contact?.comm_Contact?.comm_Contact_Category?.en_Name
      ),
      contact: contact?.comm_Contact?.value,
      primary: contact?.comm_Contact?.isVaild,
      display_in_emp_card: contact?.comm_Contact?.isShow,
      person_ID: contact.person_ID,
      contact_ID: contact.contact_ID,
      contacT_TYPE_ID: contact.comm_Contact?.contacT_TYPE_ID,
      contacT_Category_ID: contact.comm_Contact?.contacT_Category_ID,
    };
  });
}

export function empContactCard(empContacts: IEmployeeContactResponse[]): IEmployeeContactCard {
  let list: IEmployeeContactModel[] = [];
  empContacts.map((element) => {
    element.comm_Contact.comm_Contact_Type.displayName = msgTranslate(
      element.comm_Contact?.comm_Contact_Type.ar_Name,
      element.comm_Contact?.comm_Contact_Type.en_Name
    );
    if (element.comm_Contact?.isShow) list.push(element.comm_Contact);
  });

  let data: IEmployeeContactCard = list.reduce((g: any, contact: IEmployeeContactModel) => {
    g[contact.comm_Contact_Type?.displayName] = g[contact.comm_Contact_Type?.displayName] || [];
    g[contact.comm_Contact_Type?.displayName].push(contact);
    return g;
  }, {});
  return data;
}

export function employeeAddressToMap(
  employeeAddresss: IEmployeeAddressModel[]
): IEmployeeAddress[] {
  let addresses: IEmployeeAddress[] = employeeAddresss.map((employeeAddress) => {
    return <IEmployeeAddress>{
      ...employeeAddress.comm_Address,
      country: msgTranslate(
        employeeAddress.comm_Address.comm_Governorate?.comm_Country?.ar_Name,
        employeeAddress.comm_Address.comm_Governorate?.comm_Country?.en_NAme
      ),
      governorate: msgTranslate(
        employeeAddress.comm_Address.comm_Governorate?.ar_Name,
        employeeAddress.comm_Address.comm_Governorate?.en_Name
      ),
      address: employeeAddress.comm_Address.en_Name,
      primary: employeeAddress.comm_Address.isVaild,
    };
  });

  // to display in card as >> value, governorate, country
  addresses.map((employeeAddresss) => {
    employeeAddresss.allAddress = `${employeeAddresss.address}, ${
      employeeAddresss.governorate ?? "-"
    }, ${employeeAddresss.country ?? "-"}`;
  });

  return addresses;
}
