import { Injectable } from "@angular/core";
import {
  IEmployeeMilitaryDeleteParams,
  IEmployeeMilitaryModel,
  IEmployeeMilitaryRequest,
} from "app/personnel-setup/models/emp-personal-info/military-status";
import { IResponse } from "app/shared/interfaces/responces";
import { db } from "app/shared/resources/database-tabels";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class EmployeeMilitaryDataService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IEmployeeMilitaryRequest) {
    return this.post<IResponse<IEmployeeMilitaryModel>>(
      "Personnel/Employees/Create_EmployeeMilitaryStatus",
      data
    );
  }

  // update(data: IEmployeeMilitaryRequest) {
  //   return this.post<IResponse<IEmployeeMilitaryModel>>(
  //     "Personnel/Employees/Edit_EmployeeMilitaryStatus",
  //     data
  //   );
  // }

  getAll(employeeId: number) {
    return this.get<IResponse<IEmployeeMilitaryModel[]>>(
      "Personnel/Employees/GetAll_EmployeeMilitaryStatus",
      {
        params: {
          $filter: `PersonID eq ${employeeId}`,
          $expand: `${db.comm_Military_Status},${db.comm_Military_Grade}`,
        },
      }
    );
  }

  Delete(params: IEmployeeMilitaryDeleteParams) {
    return this.delete<IResponse>(
      "Personnel/Employees/Delete_EmployeeMilitaryStatus",
      {
        params: params,
      }
    );
  }
}
