import { Component, OnInit } from "@angular/core";
import { empPayrollDisplay } from "app/personnel-setup/mapper/emp-payments-info";
import {
  IEmployeePayrollDisplayModel,
  IEmployeePayrollModel,
} from "app/personnel-setup/models/payments-information/emp-payroll-data";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import { EmployeePayrollService } from "app/personnel-setup/services/employees/payments-info/employee-payroll.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { EmpPayrollDataDialogComponent } from "../emp-payroll-data-dialog/emp-payroll-data-dialog.component";

@Component({
  selector: "app-emp-payroll-data-card",
  templateUrl: "./emp-payroll-data-card.component.html",
  styleUrls: ["./emp-payroll-data-card.component.scss"],
})
export class EmpPayrollDataCardComponent extends AppHelpers implements OnInit {
  employeeId: number;

  paymentsAndVariables: IEmployeePayrollDisplayModel[];
  firstExpandedIndex: number = 0;
  isLoaded: boolean = false;
  isTableView: boolean = false;

  constructor(private employeePayrollService: EmployeePayrollService) {
    super();
    this.employeeId = EmpGeneralInfoService.EmployeeId;
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    this.paymentsAndVariables = [];
    if (this.employeeId > 0 && this.checkNoBusinessOrCompany())
      this.getAllByEmployeeId();
    else this.isLoaded = true;
  }

  getAllByEmployeeId() {
    this.paymentsAndVariables = [];
    this.employeePayrollService
      .getAll(this.employeeId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IEmployeePayrollModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.paymentsAndVariables = empPayrollDisplay(res.data);
          this.firstExpandedIndex = this.paymentsAndVariables.findIndex(
            (ele) => ele.variables.length > 0
          );
          if (this.firstExpandedIndex == -1) this.firstExpandedIndex = 0;
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  openAddEditDialog(): void {
    this.dialogsService
      .addEditDialog(
        EmpPayrollDataDialogComponent,
        {},
        { disableClose: false, minWidth: "50vw", maxWidth: "80vw", top: "40px" }
      )
      .afterClosed()
      .subscribe((updated: boolean) => {
        // if (updated) {
        //   this.isLoaded = false;
        //   this.getAllByEmployeeId();
        // }
      });
  }

  toggleTable() {
    this.isTableView = !this.isTableView;
  }
}
