import { Component, EventEmitter, Input, OnChanges, Output, SimpleChanges } from "@angular/core";
import { MatTableDataSource } from "@angular/material/table";
import { IEmployeeWorkScheduleModel, IEmpWorkScheduleDeleteParams } from "app/personnel-setup/models/emp-employment-info/work-schedule";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { EmpDataTableImplement } from "app/personnel-setup/models/implements/emp-data-table.implement";
import { EmployeeWorkScheduleService } from "app/personnel-setup/services/employees/employement-information/employee-work-schedule.service";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import { ViewsManager } from "app/shared/resources/views-manager";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { empWorkScheduleDisplay } from "app/personnel-setup/mapper/emp-employment";

@Component({
  selector: "app-emp-work-schedule-table",
  templateUrl: "./emp-work-schedule-table.component.html",
  styleUrls: ["./emp-work-schedule-table.component.scss"]
})
export class EmpWorkScheduleTableComponent extends AppHelpers implements EmpDataTableImplement, OnChanges {
  @Input() hasUpdate: boolean;
  @Input() hideActions: boolean = false;
  @Input() isMatPaginator: boolean = false;
  @Output() rowToEdit = new EventEmitter<IEmployeeWorkScheduleModel>();
  @Output() afterDelete = new EventEmitter<IEmployeeWorkScheduleModel>();
  employeeInstanceId: number = 0;
  mandatoryColomns: string[] = [
    "plan",
    "startDate",
    "toDate",
    "is_Apply_Rules",
    "actions",
  ];
  optionalColomns: string[] = [];
  dataSource: MatTableDataSource<IEmployeeWorkScheduleModel>;
  isLoaded: boolean = false;

  constructor(
    private employeeWorkScheduleService: EmployeeWorkScheduleService,
  ) {
    super();
    // this.employeeId = EmpGeneralInfoService.EmployeeId;
    this.employeeInstanceId = EmpGeneralInfoService.EmployeeInstanceId;

    this.rowFunctions = this.getSubRowFunctions(
      this.getParentViewName,
      ViewsManager.emp_work_schedule_sec
    );
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany() && this.employeeInstanceId > 0) {
      // this.getVacationDays();
      // this.getSickDays();
      // this.getWorkingDays();
      this.getList(this.firstPage);
    }
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.hasUpdate && changes.hasUpdate.currentValue && this.employeeInstanceId > 0) this.getList(this.firstPage);
    if (this.hideActions) {
      let editIndex = this.rowFunctions.basic.findIndex(e => e.name.toLowerCase() == "edit");
      this.rowFunctions.basic.splice(editIndex, 1);
    }
  }

  getList(page: number, pageSize?: number) {
    this.dataSource = new MatTableDataSource(null);
    this.isLoaded = false;
    this.page.pageIndex = page;
    if (pageSize) this.page.dialogSize = pageSize;
    let params: IPageParams = {
      PageNumber: page,
      pageSize: this.page.dialogSize,
    };
    this.employeeWorkScheduleService
      .getList(this.employeeInstanceId, params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<IEmployeeWorkScheduleModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(
            empWorkScheduleDisplay(res.data)
          );
          this.page.length = res.meta.totalItemCount;
          this.isLoaded = true;
        }
        else this.errorList(res.message.messages);
      });
  }

  toEdit(WorkSchedule: IEmployeeWorkScheduleModel) {
    this.rowToEdit.emit(WorkSchedule);
  }

  onDelete(WorkSchedule: IEmployeeWorkScheduleModel): void {
    let msg = WorkSchedule.plan;
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(WorkSchedule);
    });
  }

  delete(WorkSchedule: IEmployeeWorkScheduleModel) {
    let deleteParams: IEmpWorkScheduleDeleteParams = {
      Person_Instance_ID: this.employeeInstanceId,
      Plan_ID: WorkSchedule.plan_ID,
      FromDate: WorkSchedule.fromDate,
      ToDate: WorkSchedule.toDateOriginal,
    };
    this.showLoading("table");
    this.employeeWorkScheduleService
      .Delete(deleteParams)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          if (this.dataSource.data.length == 1 && this.currentPage > 1) {
            // if last item in list: get previous page
            this.getList(this.prevPage, this.page.dialogSize);
          }
          else {
            // if not last item in list: get current page
            this.getList(this.currentPage, this.page.dialogSize);
          }
          this.afterDelete.emit(WorkSchedule);
          // this.updated = true;
          // if (
          //   employeeWorkScheduleInfo.plan_ID == this.selectedPlan?.plan_ID &&
          //   employeeWorkScheduleInfo.fromDate == this.selectedPlan?.fromDate &&
          //   employeeWorkScheduleInfo.toDateOriginal ==
          //   this.selectedPlan?.toDateOriginal &&
          //   this.formCtrls.id.value > 0
          // )
          //   this.clearForm();
        }
        else this.errorList(res.message.messages);
        this.hideLoading("table");
      });
  }

}
