import { IEmpJobStatusPositionCardModel, IEmpStatusCardDisplayModel } from "app/personnel-setup/models/employee-employment-info";
import { HelpersFunctions, msgTranslate } from "app/shared/resources/helpers-functions";

export function empStatusCardDisplay(data: IEmpJobStatusPositionCardModel): IEmpStatusCardDisplayModel {
  return <IEmpStatusCardDisplayModel>{

    status_name: msgTranslate(data?.core_Person_Instance_Status?.core_Status?.ar_Name, data?.core_Person_Instance_Status?.core_Status?.en_Name),

    status_from_date: HelpersFunctions.mapDate(data?.core_Person_Instance_Status?.from_Date),

    status_to_date: HelpersFunctions.mapDate(data?.core_Person_Instance_Status?.to_Date),

    status_properties_name: msgTranslate(
      data?.core_Person_Instance_Status?.core_Status_Properties?.ar_Name,
      data?.core_Person_Instance_Status?.core_Status_Properties?.ar_Name),

    org_name: msgTranslate(data?.positions?.core_Org?.ar_Name, data?.positions?.core_Org?.en_Name),

    org_from_date: HelpersFunctions.mapDate(data?.positions?.orgFromDate),

    job_name: msgTranslate(data?.positions?.core_Job_Title?.ar_Name, data?.positions?.core_Job_Title?.en_Name),

    time_in_position:
      data?.positions?.time_In_Position ?
        `${data?.positions?.time_In_Position?.years} yr, ${data?.positions?.time_In_Position?.months} mon, ${data?.positions?.time_In_Position?.days} days` :
        "---",

    time_in_company: data?.time_In_Company ?
      `${data?.time_In_Company?.years} yr, ${data?.time_In_Company?.months} mon, ${data?.time_In_Company?.days} days` :
      "---",

    contract_name: msgTranslate(data?.core_Contract?.contracT_NO),

    contract_duration: data?.core_Contract ? `${HelpersFunctions.mapDate(data?.core_Contract?.starT_DATE)}, ${HelpersFunctions.mapDate(data?.core_Contract?.enD_DATE)}` : "---",
    policies: data.policies
  };
}
