import { Component, Input, OnInit } from "@angular/core";

@Component({
  selector: "card-row-item",
  templateUrl: "./card-row-item.component.html",
  styleUrls: ["./card-row-item.component.scss"],
})
export class CardRowItemComponent implements OnInit {
  @Input() label: string = "";
  @Input() labelClass: string = "col-6";
  @Input() value: string | number | boolean;
  @Input() valueClass: string = "col-6";
  @Input() isDate: boolean = false;
  @Input() isBool: boolean = false;
  @Input() displayPlaceholder: boolean = true;

  // @Output() onClick? = new EventEmitter<any>();

  constructor() {}

  ngOnInit() {
    if (this.value == "null") this.value = null;
  }

  // click(event) {
  //   this.onClick.emit(event);
  // }
}
