import { Component, EventEmitter, Input, OnChanges, Output, SimpleChanges } from "@angular/core";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { EmpDataTableImplement } from "app/personnel-setup/models/implements/emp-data-table.implement";
import { MatTableDataSource } from "@angular/material/table";
import { IEmployeeMaritalDeleteParams, IEmployeeMaritalModel } from "app/personnel-setup/models/emp-personal-info/marital-status";
import { EmployeeMaritalDataService } from "app/personnel-setup/services/employees/personal-info/employee-marital-data.service";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import { ViewsManager } from "app/shared/resources/views-manager";
import { IResponse } from "app/shared/interfaces/responces";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { employeeMaritalStatusToMap } from "app/personnel-setup/mapper/employee-personal-info";

@Component({
  selector: "app-emp-marital-status-table",
  templateUrl: "./emp-marital-status-table.component.html",
  styleUrls: ["./emp-marital-status-table.component.scss"]
})
export class EmpMaritalStatusTableComponent extends AppHelpers implements EmpDataTableImplement, OnChanges {
  @Input() hasUpdate: boolean;
  @Input() hideActions: boolean = false;
  @Input() isMatPaginator: boolean = false;
  @Output() rowToEdit = new EventEmitter<IEmployeeMaritalModel>;
  @Output() afterDelete = new EventEmitter<IEmployeeMaritalModel>;
  mandatoryColomns: string[] = [
    "maritalStatus",
    "statusDate",
    "actions",
  ];
  optionalColomns: string[] = [];
  dataSource: MatTableDataSource<IEmployeeMaritalModel>;
  employeeId: number;
  isLoaded: boolean;

  constructor(private employeeMaritalDataService: EmployeeMaritalDataService) {
    super();
    this.employeeId = EmpGeneralInfoService.EmployeeId;

    // marital status index
    this.rowFunctions = this.getSubRowFunctions(
      this.getParentViewName,
      ViewsManager.emp_personal_info_sec
    );
  }


  ngOnChanges(changes: SimpleChanges): void {
    if (changes.hasUpdate && changes.hasUpdate.currentValue && this.employeeId > 0) this.getAllMaritalStatus();
    if (this.hideActions) {
      let editIndex = this.rowFunctions.basic.findIndex(e => e.name.toLowerCase() == "edit");
      this.rowFunctions.basic.splice(editIndex, 1);
    }
  }


  getList(page: number, pageSize?: number): void {
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.employeeId > 0 && this.checkNoBusinessOrCompany()) {
      this.getAllMaritalStatus();
    }
    else this.isLoaded = true;
  }

  getAllMaritalStatus() {
    this.dataSource = new MatTableDataSource(null);
    this.isLoaded = false;
    this.employeeMaritalDataService
      .getAll(this.employeeId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IEmployeeMaritalModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(
            employeeMaritalStatusToMap(res.data)
          );
          this.isLoaded = true;
        }
        else this.errorList(res.message.messages);
      });
  }

  onDelete(employeeMaritalInfo: IEmployeeMaritalModel): void {
    let msg = this.msgTranslate(
      employeeMaritalInfo.comm_Marital_Status?.ar_Name,
      employeeMaritalInfo.comm_Marital_Status?.en_Name
    );
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(employeeMaritalInfo);
    });
  }

  delete(empMarital: IEmployeeMaritalModel) {
    let deleteParams: IEmployeeMaritalDeleteParams = {
      PersonID: this.employeeId,
      MaritalID: empMarital.comm_Marital_Status?.id,
    };
    this.showLoading('table');
    this.employeeMaritalDataService
      .Delete(deleteParams)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAllMaritalStatus();
          this.afterDelete.emit(empMarital); // emit to parent
        }
        else this.errorList(res.message.messages);
        this.hideLoading('table');
      });
  }


  toEdit(empMarital: IEmployeeMaritalModel) {
    this.rowToEdit.emit(empMarital);
  }


}
