import { Component, OnInit } from "@angular/core";
import { IEmployeeMilitaryModel } from "app/personnel-setup/models/emp-personal-info/military-status";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { EmployeeMilitaryDataService } from "app/personnel-setup/services/employees/personal-info/employee-military-data.service";
import { EmployeeMilitaryDataDialogComponent } from "../emp-military-data-dialog/emp-military-data-dialog.component";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import { employeeMilitaryStatusToMap } from "app/personnel-setup/mapper/employee-personal-info";

@Component({
  selector: "emp-military-data-card",
  templateUrl: "./emp-military-data-card.component.html",
  styleUrls: ["./emp-military-data-card.component.scss"],
})
export class EmployeeMilitaryDataCardComponent extends AppHelpers implements OnInit {
  employeeId: number;
  empMilitary: IEmployeeMilitaryModel;

  isLoaded: boolean = false;
  isTableView: boolean = false;

  constructor(
    private employeeMilitaryDataService: EmployeeMilitaryDataService
  ) {
    super();
    this.employeeId = EmpGeneralInfoService.EmployeeId;
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    this.empMilitary = null;
    if (this.employeeId > 0 && this.checkNoBusinessOrCompany())
      this.getAllEmpMilitary();
    else this.isLoaded = true;
  }

  getAllEmpMilitary() {
    this.empMilitary = null;
    this.employeeMilitaryDataService
      .getAll(this.employeeId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IEmployeeMilitaryModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          if (res.data.length > 1) {
            let sortedList: IEmployeeMilitaryModel[] = res.data;
            // get last create date
            const lastCreateDate = res.data.reduce((prev, current) =>
              new Date(prev.create_Date).getTime() >
              new Date(current.statusDate).getTime()
                ? prev
                : current
            ).create_Date;
            // get all objects with lastCreateDate
            sortedList = sortedList.filter(
              (ele) => ele.create_Date == lastCreateDate
            );
            // console.log(sortedList);

            if (sortedList.length == 1)
              this.empMilitary = employeeMilitaryStatusToMap(sortedList)[0];
            else {
              // sort by status date
              sortedList.sort(
                (a, b) =>
                  new Date(a.statusDate).getTime() -
                  new Date(b.statusDate).getTime()
              );
              this.empMilitary = employeeMilitaryStatusToMap([
                sortedList[sortedList.length - 1],
              ])[0];
            }
          }
          else this.empMilitary = employeeMilitaryStatusToMap(res.data)[0];
        }
        else this.errorList(res.message.messages);
        this.isLoaded = true;
      });
  }

  openAddEditDialog(): void {
    this.dialogsService
      .addEditDialog(EmployeeMilitaryDataDialogComponent)
      .afterClosed()
      .subscribe((updated: boolean) => {
        if (updated) {
          this.isLoaded = false;
          this.getAllEmpMilitary();
        }
      });
  }

  afterDelete(address: IEmployeeMilitaryModel) {
    if (address) {
      this.isLoaded = false;
      this.getAllEmpMilitary();
    }
  }

  toggleTable() {
    this.isTableView = !this.isTableView;
  }
}
