import { Component, OnInit } from "@angular/core";
import { EmpLocationDialogComponent } from "../emp-location-dialog/emp-location-dialog.component";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { EmployeeLocationService } from "app/personnel-setup/services/employees/employement-information/employee-location.service";
import { IResponse } from "app/shared/interfaces/responces";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import { IEmpLocationDisplayModel, IEmpLocationModel } from "app/personnel-setup/models/emp-employment-info/emp-location";
import { empLocationDisplay } from "app/personnel-setup/mapper/emp-employment";

@Component({
  selector: "app-emp-location-card",
  templateUrl: "./emp-location-card.component.html",
  styleUrls: ["./emp-location-card.component.scss"],
})
export class EmpLocationCardComponent extends AppHelpers implements OnInit {
  employeeInstanceId: number = 0;
  empLocation: IEmpLocationDisplayModel[];
  isLoaded: boolean = true;
  isTableView: boolean = false;

  constructor(private employeeLocationService: EmployeeLocationService) {
    super();
    this.employeeInstanceId = EmpGeneralInfoService.EmployeeInstanceId;
    EmpGeneralInfoService.employeeInstanceId$.subscribe((empInstanceId) => {
      this.employeeInstanceId = empInstanceId;
      if (this.employeeInstanceId > 0 && this.checkNoBusinessOrCompany()) {
        this.isLoaded = false;
        this.getAll();
      }
      else this.isLoaded = true;
    });
  }

  ngOnInit(): void {}

  // override onChangeBusinessAndCompanyId(): void {
  //   this.empLocation = null;
  //   if (this.employeeInstanceId > 0 && this.checkNoBusinessOrCompany())
  //     this.getAll();
  //   else this.isLoaded = true;
  // }

  getAll() {
    this.empLocation = null;
    this.employeeLocationService
      .getAll(this.employeeInstanceId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IEmpLocationModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          let sortedList: IEmpLocationModel[] = res.data;
          // sort by from date (from new to old)
          sortedList.sort(
            (a, b) =>
              new Date(b.from_Date).getTime() - new Date(a.from_Date).getTime()
          );
          // show only first two locations
          this.empLocation = empLocationDisplay(sortedList.slice(0, 2));
          this.isLoaded = true;
        }
        else this.errorList(res.message.messages);
      });
  }

  openAddEditDialog(): void {
    this.dialogsService
      .addEditDialog(EmpLocationDialogComponent)
      .afterClosed()
      .subscribe((updated: boolean) => {
        if (updated) {
          this.isLoaded = false;
          this.getAll();
        }
      });
  }

  afterDelete(location: IEmpLocationDisplayModel) {
    if (location) {
      this.isLoaded = false;
      this.getAll();
    }
  }

  toggleTable() {
    this.isTableView = !this.isTableView;
  }
}
