import { Injectable } from "@angular/core";
import { IEmpContractDeleteRequest } from "app/personnel-setup/models/emp-employment-info/contract";
import { IFileApiModel } from "app/shared/interfaces/files";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class EmployeeContractAttachmentService extends AppClientService {
  constructor() {
    super();
  }

  create(data: FormData) {
    return this.post<IResponse<IFileApiModel>>(
      "Personnel/Employees/Create_EmployeeContract_Attachments",
      data
    );
  }

  getAll(contractId: number) {
    return this.get<IResponse<IFileApiModel[]>>(
      "Personnel/Employees/GetAll_EmployeeContract_Attachments",
      {
        params: {
          Contract_ID: contractId,
        },
      }
    );
  }

  Delete(params: IEmpContractDeleteRequest) {
    return this.delete<IResponse>(
      "Personnel/Employees/Delete_EmployeeContract_Attachments",
      {
        params: params,
      }
    );
  }
}
