import { Injectable } from "@angular/core";
import {
  IEmployeeSapB1Model,
  IEmployeeSapB1Request,
} from "app/personnel-setup/models/emp-sap-integration/sap-b1";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { db } from "app/shared/resources/database-tabels";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class EmployeeSapB1Service extends AppClientService {
  constructor() {
    super();
  }

  create(data: IEmployeeSapB1Request) {
    return this.post<IResponse<IEmployeeSapB1Model>>(
      "Personnel/Employees/Create_EmployeeSapCostCenter",
      data
    );
  }

  update(data: IEmployeeSapB1Request) {
    return this.post<IResponse<IEmployeeSapB1Model>>(
      "Personnel/Employees/Edit_EmployeeSapCostCenter",
      data
    );
  }

  getList(employeeId: number, params: IPageParams) {
    return this.get<IResponseList<IEmployeeSapB1Model[]>>(
      "Personnel/Employees/GetList_EmployeeSapCostCenter",
      {
        params: {
          ...params,
          $filter: `Person_Instance_ID eq ${employeeId}`,
          $expand: db.saP_SystemInstance,
        },
      }
    );
  }

  getAll(employeeId: number) {
    return this.get<IResponse<IEmployeeSapB1Model[]>>(
      "Personnel/Employees/GetAll_EmployeeSapCostCenter",
      {
        params: {
          $filter: `Person_Instance_ID eq ${employeeId}`,
          $expand: db.saP_SystemInstance,
        },
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>(
      "Personnel/Employees/Delete_EmployeeSapCostCenter",
      {
        params: { Id: Id },
      }
    );
  }
}
