import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { pathMatch } from "app/shared/enums/route-path-match.enum";
import { ViewsManager } from "app/shared/resources/views-manager";
import { AddEmployeeComponent } from "./components/employees/add-employee/add-employee.component";
import { EmployeesIndexComponent } from "./components/employees/employees-index/employees-index.component";
import { AssignPlanComponent } from "./components/employees/assign-plan/assign-plan.component";
import { EmployeesActivationComponent } from "./components/employees-activation/employees-activation.component";
import { CheckIsViewAllowedGuard } from "app/shared/Guards/check-is-allowd.guard";

const routes: Routes = [
  {
    path: "employees",
    data: {view: ViewsManager.employees},
    children: [
      {
        path: "",
        redirectTo: "index",
        pathMatch: pathMatch.full,
      },
      {
        path: "index",
        component: EmployeesIndexComponent,
        data: {view: ViewsManager.employees_index},
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "add",
        component: AddEmployeeComponent,
        data: {view: ViewsManager.employee_profile},
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "edit/:employeeId",
        component: AddEmployeeComponent,
        data: {view: ViewsManager.employee_profile},
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "assign-plan",
        component: AssignPlanComponent,
        data: {view: ViewsManager.assign_plan},
        canActivate: [CheckIsViewAllowedGuard],
      },
    ],
  },
  {
    path: "employees-activation",
    component: EmployeesActivationComponent,
    data: {view: ViewsManager.employees_activation},
    canActivate: [CheckIsViewAllowedGuard],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class PersonnelSetupRoutingModule {}
