import { Component, OnInit, Output, EventEmitter } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import {
  IEmpMedicalInsuranceModel,
  IEmpMedicalInsuranceRequest,
} from "app/personnel-setup/models/employee-employment-info";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import { EmployeeMedicalInsuranceService } from "app/personnel-setup/services/employees/employement-information/employee-medical-insurance.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidator } from "app/shared/validators/custom.validator";

@Component({
  selector: "app-emp-medical-insurance-dialog",
  templateUrl: "./emp-medical-insurance-dialog.component.html",
  styleUrls: ["./emp-medical-insurance-dialog.component.scss"],
})
export class EmpMedicalInsuranceDialogComponent
  extends AppHelpers
  implements OnInit
{
  employeeId: number = 0;
  employeeInstanceId: number = 0;
  @Output() changUpdated = new EventEmitter<boolean>();

  mandatoryColomns: string[] = [
    "Ser",
    // "id",
    "medicalInsuranceCompany",
    "medicalID",
    "bloodType",
    "from",
    "to",
    "medicalSubscription",
    "companyShare",
    "employeeShare",
    "actions",
  ];
  optionalColomns: string[] = [];
  dataSource: MatTableDataSource<IEmpMedicalInsuranceModel>;

  isLoaded: boolean = false;

  form: FormGroup;
  submitted: boolean = false;

  constructor(
    private employeeMedicalInsuranceService: EmployeeMedicalInsuranceService
  ) {
    super();
    this.employeeId = EmpGeneralInfoService.EmployeeId;
    this.employeeInstanceId = EmpGeneralInfoService.EmployeeInstanceId;
  }

  ngOnInit(): void {
    this.form = this.fb.group(
      {
        id: [0, { nonNullable: true }],
        medicalInsuranceCompany: [null, [Validators.required]],
        medicalID: [null, [Validators.required]],
        from: [null, [Validators.required]],
        to: [null, [Validators.required]],
        medicalSubscription: [null, [Validators.required]],
        companyShare: [null, [Validators.required]],
        employeeShare: [null, [Validators.required]],
      },
      {
        validator: CustomValidator.compareDates("from", "to"),
      }
    );
  }

  get formCtrls() {
    return this.form.controls;
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany()) {
      if (this.employeeId > 0) this.getAll();
    }
  }

  getAll() {
    this.employeeMedicalInsuranceService
      .getAllByEmployeeId(this.employeeId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IEmpMedicalInsuranceModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(res.data);
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IEmpMedicalInsuranceRequest = {
        Person_ID: this.employeeId,
        id: this.form.value.id,
        medicalInsuranceCompany: this.form.value.medicalInsuranceCompany,
        medicalID: this.form.value.medicalID,
        from: this.form.value.from,
        to: this.form.value.to,
        medicalSubscription: this.form.value.medicalSubscription,
        companyShare: this.form.value.companyShare,
        employeeShare: this.form.value.employeeShare,
      };

      model.id == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IEmpMedicalInsuranceRequest) {
    this.showLoading();
    this.employeeMedicalInsuranceService
      .create(model)
      .subscribe((res: IResponse<IEmpMedicalInsuranceModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          this.changUpdated.emit(true);
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  update(model: IEmpMedicalInsuranceRequest) {
    this.showLoading();
    this.employeeMedicalInsuranceService
      .update(model)
      .subscribe((res: IResponse<IEmpMedicalInsuranceModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.clearForm();
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          this.changUpdated.emit(true);
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  employeeMedicalInsuranceToEdit(
    employeeMedicalInsuranceInfo: IEmpMedicalInsuranceModel
  ) {
    this.form.patchValue(employeeMedicalInsuranceInfo);
    // console.log(this.form.value);
  }

  onDelete(employeeMedicalInsuranceInfo: IEmpMedicalInsuranceModel): void {
    let msg = this.msgTranslate(employeeMedicalInsuranceInfo.medicalID);
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(employeeMedicalInsuranceInfo);
    });
  }

  delete(employeeMedicalInsuranceInfo: IEmpMedicalInsuranceModel) {
    this.showLoading();
    this.employeeMedicalInsuranceService
      .Delete(employeeMedicalInsuranceInfo.id)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          this.changUpdated.emit(true);
          if (employeeMedicalInsuranceInfo.id == this.formCtrls.id.value)
            this.clearForm();
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  clearForm() {
    this.form.reset({ id: 0 });
    this.submitted = false;
  }
}
