import {
  HelpersFunctions,
  msgTranslate,
} from "app/shared/resources/helpers-functions";
import {
  IEmpPermissionDisplayModel,
  IEmpPermissionModel,
} from "../models/emp-balances/permission";
import {
  IEmpVacationDisplayModel,
  IEmpVacationModel,
} from "../models/emp-balances/vacation";

export function empVacationDisplay(
  data: IEmpVacationModel[]
): IEmpVacationDisplayModel[] {
  return data.map((vacation) => {
    return <IEmpVacationDisplayModel>{
      ...vacation,
      vacation: msgTranslate(
        vacation.vacation_ArName,
        vacation.vacation_EnName
      ),
      startDate: HelpersFunctions.mapDate(vacation.opening_balance_date),
      endDate: HelpersFunctions.mapDate(vacation.closing_balance_date),
      balance: vacation.vacationBalanceOriginal,
      used: vacation.vacation_Done_Balance,
      remaining: vacation.vacation_balance,
    };
  });
}

export function empPermissionDisplay(
  data: IEmpPermissionModel[]
): IEmpPermissionDisplayModel[] {
  return data.map((permission) => {
    return <IEmpPermissionDisplayModel>{
      ...permission,
      permission: permission.permission_Setup_Name,
      startDate: HelpersFunctions.mapDate(permission.opening_balance_date),
      endDate: HelpersFunctions.mapDate(permission.closing_balance_date),
      balance: permission.permission_balance,
      used: permission.taken_permission_Yearly,
      remaining: permission.remaining_Balance,
      newHireCloseDate: HelpersFunctions.mapDate(permission.newHireCloseDate),
    };
  });
}
