import {Injectable} from '@angular/core';
import {IEmployeeDocumentModel,} from 'app/personnel-setup/models/emp-personal-info/employee-documents';
import {IResponse, IResponseList} from 'app/shared/interfaces/responces';
import {AppClientService} from 'app/shared/services/app-client.service';
import {db} from 'app/shared/resources/database-tabels';
import {IPageParams} from 'app/shared/interfaces/params';

@Injectable({
  providedIn: "root",
})
export class EmployeeDocumentsService extends AppClientService {
  constructor() {
    super();
  }

  create(data: FormData) {
    return this.post<IResponse<IEmployeeDocumentModel>>(
      "Personnel/Employees/Create_EmployeeIdentityInformation",
      data
    );
  }

  update(data: FormData) {
    return this.post<IResponse<IEmployeeDocumentModel>>(
      "Personnel/Employees/Edit_EmployeeIdentityInformation",
      data
    );
  }

  getList(employeeId: number, params: IPageParams) {
    return this.get<IResponseList<IEmployeeDocumentModel[]>>(
      "Personnel/Employees/GetList_EmployeeIdentityInformation",
      {
        params: {
          ...params,
          $filter: `PersonID eq ${employeeId}`,
          $expand: db.Core_Personal_Identity_Type,
        },
      }
    );
  }

  getAll(employeeId: number) {
    return this.get<IResponse<IEmployeeDocumentModel[]>>(
      "Personnel/Employees/GetAll_EmployeeIdentityInformation",
      {
        params: {
          $filter: `PersonID eq ${employeeId}`,
          $expand: db.Core_Personal_Identity_Type,
        },
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>(
      "Personnel/Employees/Delete_EmployeeIdentityInformation",
      {
        params: { Id: Id },
      }
    );
  }

  // delete by emp_attach id
  DeleteAttachment(Id: number) {
    return this.delete<IResponse>(
      "Personnel/Employees/Delete_EmployeeIdentityInformation_Attachment",
      {
        params: { ID: Id },
      }
    );
  }
}
