  afterDelete(contact: IEmployeeContactDisplay) {
    if (contact.contact_ID == this.formCtrls.id.value) this.clearForm();
    if (contact) this.hasUpdate = true;
  }

  clearForm() {
    this.form.reset({ id: 0 });
    this.submitted = false;
  }

  onChangeContactType(contact: IContactTypeModel[]) {
    if (contact)
      this.formCtrls.value.setValidators([
        Validators.pattern(contact[0]?.regularExpression),
      ]);
    else
      this.formCtrls.value.removeValidators([
        Validators.pattern(contact[0]?.regularExpression),
      ]);
  }
