  afterDelete(sapB1: IEmployeeSapB1Display) {
    if (sapB1.id == this.formCtrls.id.value) this.clearForm();
    if (sapB1) this.hasUpdate = true;
  }

  clearForm() {
    this.form.reset({id: 0});
    this.submitted = false;
  }
