import { Component, OnInit } from "@angular/core";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { IEmployeeSapB1Display, IEmployeeSapB1Model, } from "app/personnel-setup/models/emp-sap-integration/sap-b1";
import { EmpSapB1DialogComponent } from "../emp-sap-b1-dialog/emp-sap-b1-dialog.component";
import { EmployeeSapB1Service } from "app/personnel-setup/services/employees/sap-integration/employee-sap-b1.service";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import { empSapCostCenterDisplay } from "app/personnel-setup/mapper/emp-sap-integration";

@Component({
  selector: "app-emp-sap-b1-card",
  templateUrl: "./emp-sap-b1-card.component.html",
  styleUrls: ["./emp-sap-b1-card.component.scss"],
})
export class EmpSapB1CardComponent extends AppHelpers implements OnInit {
  // employeeId: number;
  employeeInstanceId: number;
  empSapB1: IEmployeeSapB1Display;

  isLoaded: boolean = false;
  isTableView: boolean = false;

  constructor(private employeeSapB1Service: EmployeeSapB1Service) {
    super();
    // this.employeeId = EmpGeneralInfoService.EmployeeId;
    this.employeeInstanceId = EmpGeneralInfoService.EmployeeInstanceId;
    EmpGeneralInfoService.employeeInstanceId$.subscribe((empInstanceId) => {
      this.employeeInstanceId = empInstanceId;
      if (this.employeeInstanceId > 0 && this.checkNoBusinessOrCompany()) {
        this.isLoaded = false;
        this.getAllByEmployeeId();
      }
      else this.isLoaded = true;
    });
  }

  ngOnInit(): void {}

  // override onChangeBusinessAndCompanyId(): void {
  //   this.empSapB1 = null;
  //   if (this.employeeInstanceId > 0 && this.checkNoBusinessOrCompany())
  //     this.getAllByEmployeeId();
  //   else this.isLoaded = true;
  // }

  getAllByEmployeeId() {
    this.empSapB1 = null;
    this.employeeSapB1Service
      .getAll(this.employeeInstanceId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IEmployeeSapB1Model[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          // // sort by id (from new to old)
          // res.data.sort(
          //   (a, b) => new Date(b.id).getTime() - new Date(a.id).getTime()
          // );
          this.empSapB1 = empSapCostCenterDisplay(
            res.data.reverse().slice(0, 1)
          )[0];
          this.isLoaded = true;
        }
        else this.errorList(res.message.messages);
      });
  }

  openAddEditDialog(): void {
    this.dialogsService
      .addEditDialog(EmpSapB1DialogComponent)
      .afterClosed()
      .subscribe((updated: boolean) => {
        if (updated) {
          this.isLoaded = false;
          this.getAllByEmployeeId();
        }
      });
  }

  afterDelete(sapB1: IEmployeeSapB1Display) {
    if (sapB1) {
      this.isLoaded = false;
      this.getAllByEmployeeId();
    }
  }

  toggleTable() {
    this.isTableView = !this.isTableView;
  }
}
