import { Injectable } from "@angular/core";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { IEmployeeActivationModel } from "../models/employees-activation";

@Injectable({
  providedIn: "root",
})
export class EmployeesActivationService extends AppClientService {
  constructor() {
    super();
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<IEmployeeActivationModel[]>>(
      "Personnel/EmployeeActivation/GetNonActivationEmployeesList",
      {
        params: params,
      }
    );
  }

  activate(id: number) {
    return this.post<IResponse<IEmployeeActivationModel[]>>(
      "Personnel/EmployeeActivation/Activate",
      null,
      {
        params: { id: id },
      }
    );
  }

  exportExcel() {
    // return this.getAll();
  }
}
