  clearForm() {
    this.form.reset({id: 0});
    this.submitted = false;
  }

  afterDelete(WorkSchedule: IEmployeeWorkScheduleModel) {
    if (
      WorkSchedule.plan_ID == this.selectedPlan?.plan_ID &&
      WorkSchedule.fromDate == this.selectedPlan?.fromDate &&
      WorkSchedule.toDateOriginal ==
      this.selectedPlan?.toDateOriginal &&
      this.formCtrls.id.value > 0
    ) this.clearForm();
    if (WorkSchedule) this.hasUpdate = true;
  }

