import { msgTranslate } from "app/shared/resources/helpers-functions";
import {
  IEmployeeSapB1Display,
  IEmployeeSapB1Model,
} from "../models/emp-sap-integration/sap-b1";
import { IEmployeeS4HanaModel } from "../models/emp-sap-integration/s4-hana";

export function empS4CostCenterDisplay(
  data: IEmployeeS4HanaModel[]
): IEmployeeS4HanaModel[] {
  return data.map((s4Item) => {
    return <IEmployeeS4HanaModel>{
      ...s4Item,
      s4SystemInstance: msgTranslate(
        s4Item.s4_SystemInstance?.ar_Name,
        s4Item.s4_SystemInstance?.en_Name
      ),
    };
  });
}

export function empSapCostCenterDisplay(
  data: IEmployeeSapB1Model[]
): IEmployeeSapB1Display[] {
  return data.map((sapItem) => {
    return <IEmployeeSapB1Display>{
      ...sapItem,
      sapSystemInstance: msgTranslate(
        sapItem.saP_SystemInstance?.ar_Name,
        sapItem.saP_SystemInstance?.en_Name
      ),
      projectCode: sapItem.proj_Code,
      defualtOcrCode1: sapItem.ocrCode1,
      defualtOcrCode2: sapItem.ocrCode2,
      defualtOcrCode3: sapItem.ocrCode3,
      defualtOcrCode4: sapItem.ocrCode4,
      defualtOcrCode5: sapItem.ocrCode5,
    };
  });
}
