import { Component, OnInit } from "@angular/core";
import { empEducationDisplay } from "app/personnel-setup/mapper/emp-career";
import { IEmployeeEducationModel } from "app/personnel-setup/models/employee-career/emp-education";
import { EmployeeEducationService } from "app/personnel-setup/services/employees/career/employee-education.service";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { EmployeeEducationDialogComponent } from "../emp-education-dialog/emp-education-dialog.component";

@Component({
  selector: "emp-education-card",
  templateUrl: "./emp-education-card.component.html",
  styleUrls: ["./emp-education-card.component.scss"],
})
export class EmployeeEducationCardComponent
  extends AppHelpers
  implements OnInit
{
  employeeId: number = 0;
  // employeeInstanceId: number = 0;
  empEducation: IEmployeeEducationModel;

  isLoaded: boolean = false;
  isTableView: boolean = false;

  constructor(private employeeEducationService: EmployeeEducationService) {
    super();
    this.employeeId = EmpGeneralInfoService.EmployeeId;
    // this.employeeInstanceId = EmpGeneralInfoService.EmployeeInstanceId;
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    this.empEducation = null;
    if (this.employeeId > 0 && this.checkNoBusinessOrCompany())
      this.getAllByEmployeeId();
    else this.isLoaded = true;
  }

  getAllByEmployeeId() {
    this.empEducation = null;
    this.employeeEducationService
      .getAll(this.employeeId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IEmployeeEducationModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          let sortedList: IEmployeeEducationModel[] = res.data;
          // sort by year (from new to old)
          sortedList.sort((a, b) => b.year - a.year);
          this.empEducation = empEducationDisplay(sortedList)[0];
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  openAddEditDialog(): void {
    this.dialogsService
      .addEditDialog(EmployeeEducationDialogComponent)
      .afterClosed()
      .subscribe((updated: boolean) => {
        if (updated) {
          this.isLoaded = false;
          this.getAllByEmployeeId();
        }
      });
  }

  afterDelete(education: IEmployeeEducationModel) {
    if (education) {
      this.isLoaded = false;
      this.getAllByEmployeeId();
    }
  }

  toggleTable() {
    this.isTableView = !this.isTableView;
  }
}
