import { Component, OnInit } from "@angular/core";
import { employeeAddressToMap } from "app/personnel-setup/mapper/emp-contact-address";
import { IEmployeeAddress, IEmployeeAddressModel, } from "app/personnel-setup/models/emp-personal-info/contact-address";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import { EmployeeAddressService } from "app/personnel-setup/services/employees/personal-info/employee-address.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { EmpAddressDialogComponent } from "../emp-address-dialog/emp-address-dialog.component";

@Component({
  selector: "emp-address-card",
  templateUrl: "./emp-address-card.component.html",
  styleUrls: ["./emp-address-card.component.scss"],
})
export class EmpAddressCardComponent extends AppHelpers implements OnInit {
  employeeId: number;
  employeeAddressInfo: IEmployeeAddress;

  isAddressLoaded: boolean = false;
  isTableView: boolean = false;

  constructor(private employeeAddressService: EmployeeAddressService) {
    super();
    this.employeeId = EmpGeneralInfoService.EmployeeId;
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    this.employeeAddressInfo = null;
    if (this.employeeId > 0 && this.checkNoBusinessOrCompany()) {
      this.GetAllEmpAddress();
    } else {
      this.isAddressLoaded = true;
    }
  }

  GetAllEmpAddress() {
    return this.employeeAddressService
      .getAll(this.employeeId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IEmployeeAddressModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.isAddressLoaded = true;
          let employeeAddressesFromNewest: IEmployeeAddress[] =
            employeeAddressToMap(res.data).reverse();
          this.employeeAddressInfo =
            employeeAddressesFromNewest.find(
              (address) => address.primary == true
            ) ?? employeeAddressesFromNewest[0];
        } else this.errorList(res.message.messages);
      });
  }

  openAddEditDialog(): void {
    this.dialogsService
      .addEditDialog(EmpAddressDialogComponent)
      .afterClosed()
      .subscribe((isUpdated: boolean) => {
        if (isUpdated) {
          this.isAddressLoaded = false;
          this.GetAllEmpAddress();
        }
      });
  }

  afterDelete(address: IEmployeeAddress) {
    if (address) {
      this.isAddressLoaded = false;
      this.GetAllEmpAddress();
    }
  }

  toggleTable() {
    this.isTableView = !this.isTableView;
  }
}
