import { Injectable } from "@angular/core";
import {
  IEditEmployeeWorkScheduleRequest,
  IEmpWorkScheduleCardModel,
  IEmpWorkScheduleDeleteParams,
  IEmployeeWorkScheduleModel,
  IEmployeeWorkScheduleRequest,
  IHoursSummary,
  IVacationSummary,
} from "app/personnel-setup/models/emp-employment-info/work-schedule";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { db } from "app/shared/resources/database-tabels";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class EmployeeWorkScheduleService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IEmployeeWorkScheduleRequest) {
    return this.post<IResponse<IEmployeeWorkScheduleModel>>(
      "Personnel/Employees/Create_EmployeeAttendencePlan",
      data
    );
  }

  update(data: IEditEmployeeWorkScheduleRequest) {
    return this.post<IResponse<IEmployeeWorkScheduleModel>>(
      "Personnel/Employees/Edit_EmployeeAttendencePlan",
      data
    );
  }

  getList(employeeInstanceId: number, params: IPageParams) {
    return this.get<IResponseList<IEmployeeWorkScheduleModel[]>>(
      "Personnel/Employees/GetList_EmployeeAttendencePlan",
      {
        params: {
          ...params,
          $filter: `Person_Instance_ID eq ${employeeInstanceId}`,
          $expand: db.att_Plan,
        },
      }
    );
  }

  getAll(employeeInstanceId: number) {
    return this.get<IResponse<IEmployeeWorkScheduleModel[]>>(
      "Personnel/Employees/GetAll_EmployeeAttendencePlan",
      {
        params: {
          $filter: `Person_Instance_ID eq ${employeeInstanceId}`,
          $expand: db.att_Plan,
        },
      }
    );
  }

  Delete(params: IEmpWorkScheduleDeleteParams) {
    return this.delete<IResponse>(
      "Personnel/Employees/Delete_EmployeeAttendencePlan",
      {
        params: params,
      }
    );
  }

  getCard(employeeInstanceId: number, StartDate: string) {
    return this.get<IResponse<IEmpWorkScheduleCardModel>>(
      "Personnel/Employees/Get_AttendancePlanCard",
      {
        params: {
          Person_Instance_ID: employeeInstanceId,
          StartDate: StartDate,
        },
      }
    );
  }

  getAnnualCasualDashboard(employeeInstanceId: number) {
    return this.get<IResponse<IVacationSummary>>(
      "Dashboards/VacationSummary/GetAnnualCasualSummery",
      {
        params: {
          Person_Instance_ID: employeeInstanceId,
        },
      }
    );
  }

  getSickLeaveDashboard(employeeInstanceId: number) {
    return this.get<IResponse<IVacationSummary>>(
      "Dashboards/VacationSummary/GetSickLeaveSummery",
      {
        params: {
          Person_Instance_ID: employeeInstanceId,
        },
      }
    );
  }

  getWorkingHoursDashboard(employeeInstanceId: number) {
    return this.get<IResponse<IHoursSummary>>(
      "Dashboards/WorkingHoursSummary/GetCurrentMonthWorkingHoursSummary",
      {
        params: {
          Person_Instance_ID: employeeInstanceId,
        },
      }
    );
  }
}
