import {Component, OnInit} from "@angular/core";
import {MatDialogRef} from "@angular/material/dialog";
import {AppHelpers} from "app/shared/resources/app-helpers";

@Component({
  selector: "emp-blood-type-section",
  templateUrl: "./emp-blood-type-section.component.html",
  styleUrls: ["./emp-blood-type-section.component.scss"],
})
export class EmpMedicalAndBloodTypeDialogComponent
  extends AppHelpers
  implements OnInit
{
  updated: boolean = false;

  constructor(
    public dialogRef: MatDialogRef<EmpMedicalAndBloodTypeDialogComponent>
  ) {
    super();
  }

  ngOnInit(): void {
    this.dialogRef.backdropClick().subscribe((ele) => {
      this.onCloseDialog();
    });
  }

  onCloseDialog() {
    this.dialogRef.close(this.updated);
  }

  changUpdated(value: boolean) {
    this.updated = value;
  }
}
