import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { empWorkScheduleDisplay } from "app/personnel-setup/mapper/emp-employment";
import {
  IEmpWorkScheduleDeleteParams,
  IEmployeeWorkScheduleModel,
} from "app/personnel-setup/models/emp-employment-info/work-schedule";
import { AssignPlanService } from "app/personnel-setup/services/employees/assign-plan.service";
import { EmployeeWorkScheduleService } from "app/personnel-setup/services/employees/employement-information/employee-work-schedule.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { IAssignPlanEmployeesRequest } from "app/personnel-setup/models/assign-plan";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";

@Component({
  selector: "app-assign-plan",
  templateUrl: "./assign-plan.component.html",
  styleUrls: ["./assign-plan.component.scss"],
})
export class AssignPlanComponent extends AppHelpers implements OnInit {
  form: FormGroup;
  submitted: boolean = false;

  mandatoryColomns: string[] = [
    "Ser",
    "employee",
    "plan",
    "startDate",
    "toDate",
    "actions",
  ];
  isLoaded: boolean = true;
  optionalColomns: string[] = [];

  dataSource: MatTableDataSource<IEmployeeWorkScheduleModel>;

  constructor(
    private employeeWorkScheduleService: EmployeeWorkScheduleService,
    private assignPlanService: AssignPlanService
  ) {
    super();
  }

  ngOnInit() {
    this.form = this.fb.group({
      id: [0, { nonNullable: true }],
      policyGroupId: [null, [Validators.required]],
      plan_ID: [null, [Validators.required]],
      employeeIds: [null, [Validators.required]],
      fromDate: [null, [Validators.required]],
      is_Apply_Rules: [null],
    });
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    // if (this.checkNoBusinessOrCompany()) {
    // }
  }

  get formCtrls() {
    return this.form.controls;
  }

  getList(page: number, pageSize?: number) {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);

    let planId = this.formCtrls.plan_ID.value;
    if (planId > 0) {
      this.isLoaded = false;
      this.page.pageIndex = page;
      if (pageSize) this.page.pageSize = pageSize;
      let params: IPageParams = {
        PageNumber: page,
        pageSize: this.page.pageSize,
      };
      this.assignPlanService
        .getList(planId, params)
        .pipe(this.cancelRequest())
        .subscribe((res: IResponseList<IEmployeeWorkScheduleModel[]>) => {
          if (res.message.messageType == MessageTypes.Success) {
            this.dataSource = new MatTableDataSource(
              empWorkScheduleDisplay(res.data)
            );
            this.isLoaded = true;
            this.page.length = res.meta.totalItemCount;
          } else this.errorList(res.message.messages);
        });
    }
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IAssignPlanEmployeesRequest = {
        EmployeeData: {
          Plan_ID: this.formCtrls.plan_ID.value,
          FromDate: HelpersFunctions.mapDateReverse(
            this.formCtrls.fromDate.value
          ),
          Is_Apply_Rules: this.formCtrls.is_Apply_Rules.value ?? false,
        },
        SelectedEmployeeIDs: this.formCtrls.employeeIds.value,
      };

      this.create(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IAssignPlanEmployeesRequest) {
    this.showLoading();
    this.assignPlanService
      .create(model)
      .subscribe((res: IResponse<IEmployeeWorkScheduleModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getList(this.currentPage);
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  onDelete(employee: IEmployeeWorkScheduleModel): void {
    let msg = employee.employee + " - " + employee.plan;
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(employee);
    });
  }

  delete(employee: IEmployeeWorkScheduleModel) {
    let deleteParams: IEmpWorkScheduleDeleteParams = {
      Person_Instance_ID: employee.person_Instance_ID,
      Plan_ID: employee.plan_ID,
      FromDate: employee.fromDate,
      ToDate: employee.toDateOriginal,
    };
    this.showLoading();
    this.employeeWorkScheduleService
      .Delete(deleteParams)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          if (this.dataSource.data.length == 1 && this.currentPage > 1) {
            // if last item in list: get previous page
            this.getList(this.prevPage, this.page.pageSize);
          } else {
            // if not last item in list: get current page
            this.getList(this.currentPage, this.page.pageSize);
          }
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  clearForm() {
    this.formCtrls.employeeIds.reset();
    this.formCtrls.fromDate.reset();
    this.formCtrls.is_Apply_Rules.reset();
    this.submitted = false;
  }
}
