  afterDelete(hiringDocument: IEmpHiringDocumentDisplayModel) {
    if (// if edit
      hiringDocument.att_Type_ID == this.selectedAttachmentTypeId && this.formCtrls.id.value > 0
    ) this.clearForm();
    if (hiringDocument) this.hasUpdate = true;
  }
