import {Injectable} from "@angular/core";
import {AppClientService} from "app/shared/services/app-client.service";

import {IResponse, IResponseList} from "app/shared/interfaces/responces";
import {db} from "app/shared/resources/database-tabels";
import {
  IEmployeeContactRequest,
  IEmployeeContactResponse,
  IEmployeeContactDeleteParams,
} from "app/personnel-setup/models/emp-personal-info/contact-address";
import {IPageParams} from "app/shared/interfaces/params";

@Injectable({
  providedIn: "root",
})
export class EmployeeContactService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IEmployeeContactRequest) {
    return this.post<IResponse<IEmployeeContactResponse>>(
      "Personnel/Employees/Create_EmployeeContactType",
      data
    );
  }

  update(data: IEmployeeContactRequest) {
    return this.post<IResponse<IEmployeeContactResponse>>(
      "Personnel/Employees/Edit_EmployeeContactType",
      data
    );
  }


  GetList(employeeId: number, params: IPageParams) {
    return this.get<IResponseList<IEmployeeContactResponse[]>>(
      "Personnel/Employees/GetList_EmployeeContactType",
      {
        params: {
          ...params,
          $filter: `Person_ID eq ${employeeId}`,
          $expand: `${db.comm_Contact}.${db.comm_Contact_Type},${db.comm_Contact}.${db.comm_Contact_Category}`,
        },
      }
    );
  }

  GetAll(employeeId: number) {
    return this.get<IResponse<IEmployeeContactResponse[]>>(
      "Personnel/Employees/GetAll_EmployeeContactType",
      {
        params: {
          $filter: `Person_ID eq ${employeeId}`,
          $expand: `${db.comm_Contact}.${db.comm_Contact_Type},${db.comm_Contact}.${db.comm_Contact_Category}`,
        },
      }
    );
  }

  Delete(params: IEmployeeContactDeleteParams) {
    return this.delete<IResponse>("Personnel/Employees/Delete_EmployeeContactType", {
      params: params,
    });
  }
}
