import { employeeDependentToMap } from "app/personnel-setup/mapper/employee-personal-info";
import { Component, OnInit } from "@angular/core";
import { IEmployeeDependantModel } from "app/personnel-setup/models/emp-personal-info/dependants";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { EmployeeDependantsDialogComponent } from "../emp-dependants-dialog/emp-dependants-dialog.component";
import { EmployeeDependantService } from "app/personnel-setup/services/employees/personal-info/employee-dependant.service";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import { FadeIn, FadeInOut, fadeOut } from "app/shared/animations/custom-animations";

@Component({
  selector: "emp-dependants-card",
  templateUrl: "./emp-dependants-card.component.html",
  styleUrls: ["./emp-dependants-card.component.scss"],
  animations: [
    FadeIn(600),
    fadeOut(300),
    FadeInOut(600, 300),
  ]
})
export class EmployeeDependantsCardComponent extends AppHelpers implements OnInit {
  employeeId: number;
  empDependant: IEmployeeDependantModel;
  isLoaded: boolean = false;
  isTableView: boolean = false;

  constructor(private employeeDependantService: EmployeeDependantService) {
    super();
    this.employeeId = EmpGeneralInfoService.EmployeeId;
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    this.empDependant = null;
    if (this.employeeId > 0 && this.checkNoBusinessOrCompany())
      this.getAllDependant();
    else this.isLoaded = true;
  }

  getAllDependant() {
    this.empDependant = null;
    this.employeeDependantService
      .getAll(this.employeeId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IEmployeeDependantModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          // get oldest emergency one
          let employeeDependentFromOld: IEmployeeDependantModel[] =
            employeeDependentToMap(res.data);
          this.empDependant =
            employeeDependentFromOld.find(
              (dependent) => dependent.isEmergcy == true
            ) ?? employeeDependentFromOld[0];
          this.isLoaded = true;
        }
        else this.errorList(res.message.messages);
      });
  }

  openAddEditDialog(): void {
    this.dialogsService
      .addEditDialog(EmployeeDependantsDialogComponent)
      .afterClosed()
      .subscribe((updated: boolean) => {
        if (updated) {
          this.isLoaded = false;
          this.getAllDependant();
        }
      });
  }


  afterDelete(Dependant: IEmployeeDependantModel) {
    if (Dependant) {
      this.isLoaded = false;
      this.getAllDependant();
    }
  }

  toggleTable() {
    this.isTableView = !this.isTableView;
  }
}
