import {
  HelpersFunctions,
  msgTranslate,
} from "app/shared/resources/helpers-functions";
import { IEmployeeBankingAccountModel } from "../models/payments-information/emp-bank-account";
import {
  IEmployeePayVariableDisplayModel,
  IEmployeePayrollDisplayModel,
  IEmployeePayrollModel,
  IEmployeePayrollVariableModel,
} from "../models/payments-information/emp-payroll-data";

export function empBankingAccountDisplay(
  data: IEmployeeBankingAccountModel[]
): IEmployeeBankingAccountModel[] {
  return data.map((bankAccount) => {
    return <IEmployeeBankingAccountModel>{
      ...bankAccount,
      bank: msgTranslate(
        bankAccount.comm_Bank?.ar_Name,
        bankAccount.comm_Bank?.en_Name
      ),
      startDate: HelpersFunctions.mapDate(bankAccount.from_Date),
    };
  });
}

export function empPayrollDisplay(
  data: IEmployeePayrollModel
): IEmployeePayrollDisplayModel[] {
  // global section
  let list: IEmployeePayrollDisplayModel[] = [
    {
      paymentId: 0,
      payment: msgTranslate(" ", "Global Variable"),
      variables: [],
    },
  ];
  // payments section
  data.lstPayments.map((payment) =>
    list.push({
      paymentId: payment.id,
      payment: msgTranslate(payment.ar_Name, payment.en_Name),
      variables: [],
    })
  );

  // global variables
  data.lstFixedVAriableGlobal.map((variable) =>
    list[0].variables.push(...getVariableList(variable))
  );

  // payments variables
  data.perssonPeriodLsts.map((ele) => {
    let paymentIndex = getIndexInList(list, ele.payment?.id);
    ele.lstpersperiodcalculations.forEach((variable) =>
      list[paymentIndex].variables.push(...getVariableList(variable))
    );
  });

  data.lstFixedVAriable.map((variable) => {
    if (
      (variable.paY_Variable?.pay_Payment_Variable?.length != 0 &&
        getIndexInList(
          list,
          variable.paY_Variable?.pay_Payment_Variable[0].Payment_ID
        ) >= 0) ||
      variable.paY_Variable?.pay_Payment_Variable?.length == 0
    ) {
      let paymentIndex = getIndexInList(
        list,
        variable.paY_PeriodCalculation?.payment_ID
      );
      if (
        data.lstAuthorize.find(
          (ele) =>
            ele.variable_ID == variable.variable_ID &&
            ele.payment_ID == list[paymentIndex].paymentId
        )
      )
        list[paymentIndex].variables.push(...getVariableList(variable));
    }
  });

  return list;
}

function getIndexInList(
  list: IEmployeePayrollDisplayModel[],
  paymentId: number
) {
  return list.findIndex((ele) => ele.paymentId == paymentId);
}

function getVariableList(payrollElement: IEmployeePayrollVariableModel) {
  let variables: IEmployeePayVariableDisplayModel[] = [];
  variables.push({
    variable: msgTranslate(
      payrollElement.paY_Variable?.arName,
      payrollElement.paY_Variable?.name
    ),
    value: payrollElement.variable_Value,
  });
  return variables;
}
