  afterDelete(s4Hana: IEmployeeS4HanaModel) {
    if (s4Hana.id == this.formCtrls.id.value) this.clearForm();
    if (s4Hana) this.hasUpdate = true;
  }

  clearForm() {
    this.form.reset({id: 0});
    this.submitted = false;
  }

  changeCostCenterText(costCenters: IS4CostCenter[]) {
    this.formCtrls.costCenter_Text.setValue(
      costCenters[0]?.costCenter_Text ?? null
    );
  }
