import { Component, OnInit } from "@angular/core";
import { empBankingAccountDisplay } from "app/personnel-setup/mapper/emp-payments-info";
import { IEmployeeBankingAccountModel } from "app/personnel-setup/models/payments-information/emp-bank-account";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import { EmployeeBankingAccountService } from "app/personnel-setup/services/employees/payments-info/employee-banking-account.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { EmpBankingAccountDataDialogComponent } from "../emp-banking-account-data-dialog/emp-banking-account-data-dialog.component";

@Component({
  selector: "app-emp-banking-account-data-card",
  templateUrl: "./emp-banking-account-data-card.component.html",
  styleUrls: ["./emp-banking-account-data-card.component.scss"],
})
export class EmpBankingAccountDataCardComponent
  extends AppHelpers
  implements OnInit {
  employeeInstanceId: number;
  employeeBankingAccountInfo: IEmployeeBankingAccountModel;
  isLoaded: boolean = false;
  isTableView: boolean = false;

  constructor(
    private employeeBankingAccountService: EmployeeBankingAccountService
  ) {
    super();
    this.employeeInstanceId = EmpGeneralInfoService.EmployeeInstanceId;
    EmpGeneralInfoService.employeeInstanceId$.subscribe((empInstanceId) => {
      this.employeeInstanceId = empInstanceId;
      if (this.employeeInstanceId > 0 && this.checkNoBusinessOrCompany()) {
        this.isLoaded = false;
        this.getAll();
      }
      else this.isLoaded = true;
    });
  }

  ngOnInit(): void {}

  // override onChangeBusinessAndCompanyId(): void {
  //   this.employeeBankingAccountInfo = null;
  //   if (this.employeeInstanceId > 0 && this.checkNoBusinessOrCompany())
  //     this.getAll();
  //   else this.isLoaded = true;
  // }

  getAll() {
    this.employeeBankingAccountInfo = null;
    this.employeeBankingAccountService
      .getAll(this.employeeInstanceId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IEmployeeBankingAccountModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          // sort by start date (from new to old)
          res.data.sort(
            (a, b) =>
              new Date(b.from_Date).getTime() - new Date(a.from_Date).getTime()
          );
          this.employeeBankingAccountInfo = empBankingAccountDisplay(
            res.data.slice(0, 1)
          )[0];
          this.isLoaded = true;
        }
        else this.errorList(res.message.messages);
      });
  }

  openAddEditDialog(): void {
    this.dialogsService
      .addEditDialog(EmpBankingAccountDataDialogComponent)
      .afterClosed()
      .subscribe((updated: boolean) => {
        if (updated) {
          this.isLoaded = false;
          this.getAll();
        }
      });
  }

  afterDelete(bankingAccount: IEmployeeBankingAccountModel) {
    if (bankingAccount) {
      this.isLoaded = false;
      this.getAll();
    }
  }

  toggleTable() {
    this.isTableView = !this.isTableView;
  }
}
