import { Injectable } from "@angular/core";
import {
  IEmpSupervisorDeleteRequest,
  IEmpSupervisorModel,
  IEmpSupervisorRequest,
} from "app/personnel-setup/models/emp-employment-info/supervisor";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class EmployeeSupervisorService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IEmpSupervisorRequest) {
    return this.post<IResponse<IEmpSupervisorModel>>(
      "Personnel/Employees/Create_EmployeeSupervisor",
      data
    );
  }

  getList(employeeInstanceId: number, params: IPageParams) {
    return this.get<IResponseList<IEmpSupervisorModel[]>>(
      "Personnel/Employees/GetList_EmployeeSupervisor",
      {
        params: {
          ...params,
          prtid: employeeInstanceId,
        },
      }
    );
  }

  getAll(employeeInstanceId: number) {
    return this.get<IResponse<IEmpSupervisorModel[]>>(
      "Personnel/Employees/GetAll_EmployeeSupervisor",
      {
        params: {
          prtid: employeeInstanceId,
        },
      }
    );
  }

  Delete(params: IEmpSupervisorDeleteRequest) {
    return this.delete<IResponse>(
      "Personnel/Employees/Delete_EmployeeSupervisor",
      {
        params: params,
      }
    );
  }
}
