import { Component, OnInit } from "@angular/core";
import { IEmployeeMaritalModel } from "app/personnel-setup/models/emp-personal-info/marital-status";
import { IEmployee } from "app/personnel-setup/models/employees";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import { EmployeeMaritalDataService } from "app/personnel-setup/services/employees/personal-info/employee-marital-data.service";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { EmployeePersonalInfoDialogComponent } from "../emp-personal-info-dialog/emp-personal-info-dialog.component";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";

@Component({
  selector: "emp-personal-info-card",
  templateUrl: "./emp-personal-info-card.component.html",
  styleUrls: ["./emp-personal-info-card.component.scss"],
})
export class EmployeePersonalInfoCardComponent extends AppHelpers implements OnInit {
  employeeId: number;
  empMarital: IEmployeeMaritalModel;
  employee: IEmployee;
  isPersonalLoaded: boolean = false;
  isMaritalLoaded: boolean = false;
  isTableView: boolean = false;

  constructor(
    private empGeneralInfoService: EmpGeneralInfoService,
    private employeeMaritalDataService: EmployeeMaritalDataService
  ) {
    super();
    this.employeeId = EmpGeneralInfoService.EmployeeId;
  }

  ngOnInit(): void {
    this.employee = this.empGeneralInfoService.employee$.getValue();
    this.empGeneralInfoService.employee$.subscribe((emp) => {
      this.employee = emp;
      this.personalLoadedFun();
    });
    this.personalLoadedFun();
  }

  personalLoadedFun() {
    this.isPersonalLoaded = false;
    if (this.employee != null && this.employeeId > 0)
      this.isPersonalLoaded = true;
    if (this.employee == null && this.employeeId == 0)
      this.isPersonalLoaded = true;
  }

  override onChangeBusinessAndCompanyId(): void {
    this.empMarital = null;
    if (this.employeeId > 0 && this.checkNoBusinessOrCompany()) this.getAll();
    else this.isMaritalLoaded = true;
  }

  getAll() {
    this.empMarital = null;
    this.employeeMaritalDataService
      .getAll(this.employeeId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IEmployeeMaritalModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          let sortedList: IEmployeeMaritalModel[] = res.data;
          // if at least one with status date sort by status date // else sort by create date
          // from old to new
          if (sortedList.some((ele) => ele.statusDate))
            sortedList.sort(
              (a, b) =>
                new Date(a.statusDate).getTime() -
                new Date(b.statusDate).getTime()
            );
          else
            sortedList.sort(
              (a, b) =>
                new Date(a.create_Date).getTime() -
                new Date(b.create_Date).getTime()
            );
          // console.log(sortedList);
          this.empMarital = sortedList[sortedList.length - 1];
        }
        else this.errorList(res.message.messages);
        this.isMaritalLoaded = true;
      });
  }

  openAddEditDialog(): void {
    this.dialogsService
      .addEditDialog(EmployeePersonalInfoDialogComponent)
      .afterClosed()
      .subscribe((updated: boolean) => {
        if (updated) {
          this.isMaritalLoaded = false;
          this.getAll();
        }
        // if (res?.isPersonal) {
        // 	this.empGeneralInfoService.getEmployee(this.employeeId);
        // } else if (res?.isMarital) {
        // 	this.getEmpAll_Military();
        //   this.empGeneralInfoService.getEmployee(this.employeeId);
        // }
      });
  }

  afterDelete(Marital: IEmployeeMaritalModel) {
    if (Marital) {
      this.isMaritalLoaded = false;
      this.getAll();
    }
  }

  toggleTable() {
    this.isTableView = !this.isTableView;
  }
}
