import { msgTranslate } from "app/shared/resources/helpers-functions";
import { IEmployeeActivationModel } from "../models/employees-activation";

export function empActivationDisplay(
  data: IEmployeeActivationModel[]
): IEmployeeActivationModel[] {
  return data.map((ele) => {
    return <IEmployeeActivationModel>{
      ...ele,
      employee: msgTranslate(
        ele.core_Person?.ar_Name,
        ele.core_Person?.en_Name
      ),
      job: msgTranslate(
        ele.core_Person_Instance_Job_Title[0]?.core_Job_Title?.ar_Name,
        ele.core_Person_Instance_Job_Title[0]?.core_Job_Title?.en_Name
      ),
    };
  });
}
