import { Component, Input, OnInit } from "@angular/core";
import { FormGroup } from "@angular/forms";
import {
  IEmpContractAttachmentParamsRequest,
  IEmpContractDeleteRequest,
} from "app/personnel-setup/models/emp-employment-info/contract";
import {
  EmployeeContractAttachmentService
} from "app/personnel-setup/services/employees/employement-information/employee-contract-attachment.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IFileApiModel, IFileData } from "app/shared/interfaces/files";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "app-emp-contract-attachment-section",
  templateUrl: "./emp-contract-attachment-section.component.html",
  styleUrls: ["./emp-contract-attachment-section.component.scss"],
})
export class EmpContractAttachmentSectionComponent
  extends AppHelpers
  implements OnInit {
  @Input() contractId: number;
  // @Output() changUpdated = new EventEmitter<boolean>();

  isLoaded: boolean = false;

  form: FormGroup;
  submitted: boolean = false;
  files: IFileData[] = [];
  removeFiles: boolean = false;
  addAttachParams: IEmpContractAttachmentParamsRequest;

  constructor(
    private employeeContractAttachmentService: EmployeeContractAttachmentService
  ) {
    super();
  }

  ngOnInit(): void {
    this.form = this.fb.group({
      // id: [0, { nonNullable: true }],
      files: [null],
    });

    this.addAttachParams = {
      ContractID: this.contractId,
    };
  }

  get formCtrls() {
    return this.form.controls;
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany() && this.contractId > 0) this.getAll();
  }

  getAll() {
    this.showLoading("attachments");
    this.employeeContractAttachmentService
      .getAll(this.contractId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IFileApiModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.files = [];
          res.data.forEach((attach) => {
            this.files.push({
              id: attach.id,
              name: attach.en_Name,
              path: "/" + attach.path,
              size: attach.size,
              type: attach.mime,
            });
          });
          this.isLoaded = true;
        }
        else this.errorList(res.message.messages);
        this.hideLoading("attachments");
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    this.removeFiles = false;
    if (this.form.valid) this.showLoading();
  }

  afterCreate() {
    this.clearForm();
    this.getAll();
    // this.changUpdated.emit(true);
  }

  delete(file: IFileData) {
    if (file.id) {
      let deleteParams: IEmpContractDeleteRequest = {
        ContractID: this.contractId,
        AttachmentID: file.id,
      };

      this.showLoading("attachments");
      this.employeeContractAttachmentService
        .Delete(deleteParams)
        .subscribe((res: IResponse) => {
          if (res.message.messageType == MessageTypes.Success) {
            this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
            this.getAll();
            // this.changUpdated.emit(true);
          }
          else this.errorList(res.message.messages);
          this.hideLoading("attachments");
        });
    }
  }

  clearForm() {
    this.form.reset({id: 0});
    this.submitted = false;
    this.removeFiles = true;
  }
}
