import { Injectable } from "@angular/core";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

import { db } from "app/shared/resources/database-tabels";
import {
	IEmployeeAddressRequest,
	IEmployeeAddressModel,
	IEmployeeAddressDeleteParams,
} from "app/personnel-setup/models/emp-personal-info/contact-address";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
	providedIn: "root",
})
export class EmployeeAddressService extends AppClientService {
	constructor() {
		super();
	}

	create(data: IEmployeeAddressRequest) {
		return this.post<IResponse<IEmployeeAddressModel>>(
			"Personnel/Employees/Create_EmployeeAddress",
			data
		);
	}

	update(data: IEmployeeAddressRequest) {
		return this.post<IResponse<IEmployeeAddressModel>>(
			"Personnel/Employees/Edit_EmployeeAddress",
			data
		);
	}

	GetList(employeeId: number, params: IPageParams) {
		return this.get<IResponseList<IEmployeeAddressModel[]>>(
			"Personnel/Employees/GetList_EmployeeAddress",
			{
				params: {
					...params,
					$filter: `Person_ID eq ${employeeId}`,
					$expand: `${db.comm_Address}.${db.comm_Governorate}.${db.comm_Country}`,
				},
			}
		);
	}

	getAll(employeeId: number) {
		return this.get<IResponse<IEmployeeAddressModel[]>>(
			"Personnel/Employees/GetAll_EmployeeAddress",
			{
				params: {
					$filter: `Person_ID eq ${employeeId}`,
					$expand: `${db.comm_Address}.${db.comm_Governorate}.${db.comm_Country}`,
				},
			}
		);
	}

	Delete(params: IEmployeeAddressDeleteParams) {
		return this.delete<IResponse>("Personnel/Employees/Delete_EmployeeAddress", {
			params: params,
		});
	}
}
